import "@nucleus/polyfill/src/_polyfill";
import { Component, Factory, Subject } from "@nucleus/core/src/_core";

export default class NucleusAMediaYoutube extends Component {

    /**
     * @inheritDoc
     */
    static get rootClassName() {
        return "nuc-a-media-youtube";
    }

    /**
     * Creates an instance of NucleusAMediaYoutube.
     *
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);

        this._playerElement = this.rootElement.querySelector(".nuc-a-media-youtube__player");
        this._playerConfig = JSON.parse(this.rootElement.dataset.config);
        this._player = null;

        NucleusAMediaYoutube._setYouTubeIframeAPIScript();
        this._initPlayerOnIntersection();
    }

    /**
     * Method called as soon as API is ready.
     */
    static onYouTubeIframeAPIReady() {
        window.NucleusAMediaYoutube.isYouTubeAPIReady = true;
        window.NucleusAMediaYoutube.youTubeAPIReadySubject.notify(this);
    }

    /**
     * Set YouTube iframe-API script to end of body if not already done.
     * @private
     */
    static _setYouTubeIframeAPIScript() {
        if (!window.NucleusAMediaYoutube.isYouTubeAPIScriptLoaded) {
            let script = document.createElement("script");
            script.src = "https://www.youtube.com/iframe_api";

            document.head.appendChild(script);
            window.NucleusAMediaYoutube.isYouTubeAPIScriptLoaded = true;
        }
    }

    /**
     * Initialize the player if container gets visible.
     * @private
     */
    _initPlayerOnIntersection() {
        let observer = new IntersectionObserver((entries, observer) => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    if (window.NucleusAMediaYoutube.isYouTubeAPIReady) {
                        this._initPlayer();
                    } else {
                        window.NucleusAMediaYoutube.youTubeAPIReadySubject.subscribe(
                            () => this._initPlayer()
                        );
                    }
                    observer.unobserve(entry.target);
                }
            });
        });
        observer.observe(this.rootElement);
    }

    /**
     * Initialize the YouTube player.
     * @private
     */
    _initPlayer() {
        this._player = new YT.Player(this._playerElement.id, this._playerConfig);
    }

    /**
     * Make the stopVideo Method of the Player API accessible.
     * @public
     */
    stopVideo() {
        if (this._player) {
            this._player.stopVideo();
        }
    }
}

window.NucleusAMediaYoutube = {
    isYouTubeAPIReady: false,
    isYouTubeAPIScriptLoaded: false,
    youTubeAPIReadySubject: new Subject()
};
window.onYouTubeIframeAPIReady = NucleusAMediaYoutube.onYouTubeIframeAPIReady;

Factory.registerComponent(NucleusAMediaYoutube);
