import "@nucleus/polyfill/src/_polyfill";

export default class NucleusEAnimationPreset {
    /**
     * Object of added presets.
     * @type {{}}
     * @private
     */
    static _presets = {};

    /**
     * Resolves animation with preset name.
     * @param {String} name
     * @returns {NucleusEAnimationSchema[]}
     */
    static resolvePreset(name) {
        if (NucleusEAnimationPreset._presets[name]) {
            return NucleusEAnimationPreset._presets[name];
        } else {
            console.warn(`[nucleus] Animation preset with name "${name}" doesn't exist.`);
        }

        return [];
    }

    /**
     * Adds preset to project
     * @param {String} name
     * @param {NucleusEAnimationSchema} preset
     */
    static addPreset(name, preset) {
        NucleusEAnimationPreset._presets[name] = preset;
    }
}