import "@nucleus/polyfill/src/_polyfill";
import {Component, Factory, Subject} from "@nucleus/core/src/_core";

export default class NucleusAObject extends Component {

    /**
     * @inheritDoc
     */
    static get rootClassName() {
        return "nuc-a-object";
    }

    /**
     * Creates an instance of NucleusAObject.
     *
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);

        /**
         * @type {boolean}
         * @private
         */
        this._isLoaded = false;

        /**
         * @type {Subject}
         * @private
         */
        this._loadSubject = new Subject();

        this.rootElement.addEventListener("load", (event) => {
            // async execution because sometimes the document is not fully loaded and parsed.
            setTimeout(() => {
                this._isLoaded = true;
                this._loadSubject.notify(this, event);
            }, 250);
        });

        this.rootElement.data = this.rootElement.dataset.data;
    }

    /**
     * Return a subject for the load event.
     * @returns {Subject}
     */
    get loadSubject() {
        return this._loadSubject;
    }

    /**
     * Returns true if the object is already loaded.
     * @returns {boolean}
     */
    get isLoaded() {
        return this._isLoaded;
    }

    /**
     * Return the content document of the object.
     * @returns {Document}
     */
    get contentDocument() {
        return this.rootElement.contentDocument
    }
}

Factory.registerComponent(NucleusAObject);
