import "@nucleus/polyfill/src/_polyfill";
import { Component, Factory } from "@nucleus/core/src/_core";

export default class NucleusMFlexContainer extends Component {

    /**
     * @inheritDoc
     */
    static get rootClassName() {
        return "nuc-m-flex-container";
    }

    /**
     * Creates an instance of NucleusMFlexContainer.
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);

        this._embeddedElement = this.rootElement.querySelector(".nuc-m-flex-container__container");
    }

    /**
     * Returns the element, which directly contains the embedded content.
     * @return {Element | any}
     */
    get embeddedElement() {
        return this._embeddedElement;
    }
}

Factory.registerComponent(NucleusMFlexContainer);
