import "@nucleus/polyfill/src/_polyfill";
import { Factory } from "@nucleus/core/src/_core";
import NucleusOStage from "@nucleus/o-stage";
import SchwarzOHeader from "@schwarz/o-header";
import SchwarzOStickyBar from "@schwarz/o-sticky-bar";

export default class SchwarzOBanner extends NucleusOStage {
    /**
     * @inheritDoc
     */
    static get rootClassName() {
        return "scwz-o-banner";
    }

    /**
     * Creates an instance of SchwarzOBanner.
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);
    }

    /**
     * Return offset top value.
     * @returns {number}
     * @private
     */
    get _offSetTop() {
        let header = document.querySelector(SchwarzOHeader.rootSelector);

        if (header) {
            return header.offsetHeight;
        } else {
            return 0;
        }
    }

    /**
     * Return offset bottom value.
     * @returns {number}
     * @private
     */
    get _offSetBottom() {
        let footer = document.querySelector(SchwarzOStickyBar.rootSelector);

        if (footer) {
            return footer.offsetHeight;
        } else {
            return 0;
        }
    }
}

Factory.registerComponent(SchwarzOBanner);