import "@nucleus/polyfill/src/_polyfill";
import NucleusAButton from '@nucleus/a-button';
import NucleusMTabItem from '@nucleus/m-tab-item';
import NucleusATabContent from '@nucleus/a-tab-content';
import { Component, Factory, Subject } from "@nucleus/core/src/_core";
import { KEYCODES } from "@nucleus/core/src/_constants";
import { BREAKPOINTS } from "@nucleus/core/src/_constants";
import Swiper from "swiper";
import EventUtils from "@nucleus/core/src/_eventUtils";


export default class NucleusOTabContainer extends Component {

    static get rootClassName() {
        return "nuc-o-tab-container";
    }

    /**
     * Creates an instance of NucleusMTabContainer.
     * @private
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);

        /**
         * @type {NucleusMTabItem[]}
         * @private
         */
        this._tabs = Factory.createComponents(
            this.rootElement.querySelectorAll(NucleusMTabItem.rootSelector)
        );

        /**
         * @type {NucleusATabContent[]}
         * @private
         */
        this._contents = Factory.createComponents(
            this.rootElement.querySelectorAll(NucleusATabContent.rootSelector)
        );

        /**
         * @type {Component}
         * @private
         */
        this._navContainer = Factory.createComponent(rootElement.querySelector(".nuc-o-tab-container__navigation-wrapper"));

        /**
         * @type {NucleusAButton}
         * @private
         */
        this._buttonPrev = Factory.createComponent(this.querySelector(".nuc-o-tab-container__button-previous"));

        /**
         *
         * @type {NucleusAButton}
         * @private
         */
        this._buttonNext = Factory.createComponent(this.querySelector(".nuc-o-tab-container__button-next"));

        /**
         * @type {Subject}
         * @private
         */
        this._tabChangeSubject = new Subject();

        /**
         * @type {NucleusMTabItem[]}
         * @private
         */
        this._tabItems = [];

        /**
         * @type {NucleusATabContent[]}
         * @private
         */
        this._tabContents = [];

        /**
         * @type {NucleusMTabItem}
         * @private
         */
        this._selectedTabItem = null;

        /**
         * Handle interaction for every tab.
         */
        this._tabs.forEach((tabItem, tabItemIndex) => {
            tabItem.stateSubject.subscribe(currentTabItem => {
                if (currentTabItem.state === NucleusMTabItem.States.SELECTED) {
                    this._updateTabs(currentTabItem);
                }
            });
            tabItem.keyUpSubject.subscribe((currentTabItem, event) => {
                this._handleKeyUp(tabItemIndex, event);
            });
            this._tabItems.push(tabItem);
        });

        /**
         * Save tab content elements.
         */
        this._contents.forEach(tabContentElement => {
            this._tabContents.push(tabContentElement);
        });
        this._connectTabItemAndTabContent();

        this._selectDefaultTab();

        /**
         * @type {MediaQueryList}
         * @private
         */
        this._sliderBreakpoint = window.matchMedia("(min-width: " + BREAKPOINTS.sm + "px)");

        /**
         * @type {Swiper}
         * @private
         */
        this._slider = null;

        window.addEventListener("resize", EventUtils.debounce(() => this._checkSlider(), 250));
        this._checkSlider();
    }

    /**
     * Handles keyboard navigation.
     * @param {Number} tabItemIndex
     * @param {Event} event
     * @private
     */
    _handleKeyUp(tabItemIndex, event) {
        let newIndex = null;
        const key = event.code || event.keyCode;
        switch (key) {
            case KEYCODES.HOME:
            case "Home":
                newIndex = 0;
                break;
            case KEYCODES.END:
            case "End":
                newIndex = this._tabItems.length - 1;
                break;
            case KEYCODES.LEFT:
            case "ArrowLeft":
                if (tabItemIndex !== 0) {
                    newIndex = tabItemIndex - 1;
                    this._slider.slidePrev();
                }
                break;
            case KEYCODES.RIGHT:
            case "ArrowRight":
                if (tabItemIndex !== this._tabItems.length - 1) {
                    newIndex = tabItemIndex + 1;
                    this._slider.slideNext();
                }
                break;
            case KEYCODES.SPACE:
            case KEYCODES.ENTER:
            case "Space":
            case "Enter":
            case "NumpadEnter":
                newIndex = tabItemIndex;
                break;
        }
        if (newIndex !== null) {
            const newCurrentTabItem = this._tabItems[newIndex];
            newCurrentTabItem.state = NucleusMTabItem.States.SELECTED;
            newCurrentTabItem.focus();
        }
    }

    /**
     * Update tabs.
     * @param {NucleusMTabItem} selectedTabItem
     * @private
     */
    _updateTabs(selectedTabItem) {
        // reset tabs and set previous and next states
        this._selectedTabItem = selectedTabItem;
        this._tabItems.forEach(tabItem => {
            if (tabItem.rootElement !== selectedTabItem.rootElement) {
                if (tabItem.rootElement === selectedTabItem.rootElement.previousElementSibling) {
                    tabItem.state = NucleusMTabItem.States.PREVIOUS;
                } else if (tabItem.rootElement === selectedTabItem.rootElement.nextElementSibling) {
                    tabItem.state = NucleusMTabItem.States.NEXT;
                } else {
                    tabItem.state = NucleusMTabItem.States.NONE;
                }
            }
        });

        this._tabChangeSubject.notify(this);
    }

    /**
     * connect tab-item and corresponding tab-content
     * @private
     */
    _connectTabItemAndTabContent() {
        this._tabContents.forEach((tabContent, i) => {
            let tabItem = this._tabItems[i];
            tabItem.linkToContent(tabContent);
            tabContent.linkToTab(tabItem);
        });
    }

    /**
     * select default tab if defined, otherwise select the first tab
     * @private
     */
    _selectDefaultTab() {
        let selectedDefaultTabItem = this._tabItems.find(tabItem => tabItem.state === NucleusMTabItem.States.SELECTED);

        if (!selectedDefaultTabItem) {
            selectedDefaultTabItem = this._tabItems[0];
        }

        selectedDefaultTabItem.state = NucleusMTabItem.States.SELECTED;
    }

    /**
     * Subject for tab change events. This subject triggers a event every time the active tab is changed.
     * @return {Subject}
     */
    get tabChangeSubject() {
        return this._tabChangeSubject;
    }

    /**
     * Returns the selected tab item.
     * @return {NucleusMTabItem}
     */
    get selectedTabItem() {
        return this._selectedTabItem;
    }

    /**
     * Returns all tab item elements.
     * @return {NucleusMTabItem[]}
     */
    get tabItems() {
        return this._tabItems;
    }

    /**
     * Initialise or destroy slider based given on viewport width.
     * @private
     */
    _checkSlider() {
        if (this._sliderBreakpoint.matches) {
            this._initSlider();
        } else {
            this._destroySlider();
        }
    }

    /**
     * Initialize the slider.
     * @private
     */
    _initSlider() {
        this._slider = new Swiper(this._navContainer.rootElement, {
            slidesPerView: "auto",
            loop: false,
            freeMode: false,
            simulateTouch: false,
            keyboard: {
                enabled: false
            },
            wrapperClass: "nuc-o-tab-container__items",
            slideClass: "nuc-m-tab-item",
            navigation: {
                nextEl: this._buttonNext.rootElement,
                prevEl: this._buttonPrev.rootElement,
                disabledClass: "hidden@all"
            },
            a11y: {
                enabled: false
            }
        });
    }

    /**
     * Destroy the slider.
     * @private
     */
    _destroySlider() {
        if (this._slider !== null) {
            this._slider.destroy(true, true);
            this._slider = null;
        }
    }

    /**
     * Initialize slider.
     * @public
     */
    initContainer() {
        this._checkSlider();
    }
}

Factory.registerComponent(NucleusOTabContainer);
