import "@nucleus/polyfill/src/_polyfill";
import { Component, Factory } from "@nucleus/core/src/_core";
import {KEYCODES} from "@nucleus/core/src/_constants";
import NucleusOTabContainer from "@nucleus/o-tab-container";

export default class SchwarzOLocations extends Component {

    /**
     * @inheritDoc
     */
    static get rootClassName() {
        return "swrz-o-locations";
    }

    /**
     * Creates an instance of SchwarzOLocations.
     *
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);

        setTimeout(() => {
            this.rootElement.querySelectorAll('[data-map-tab]').forEach(element => element.classList.add("hidden@all"));
            this.rootElement.querySelector("[data-map-tab]").classList.remove("hidden@all");

            var brandNavItem = this.rootElement.querySelector(".swrz-o-locations__brand-nav-item");
            if (brandNavItem) {
                brandNavItem.classList.add("swrz-o-locations__brand-nav-item--selected");
            }

            this.rootElement.style.visibility = "visible";
        }, 0);

        var branchTriggers = this.rootElement.querySelectorAll("[data-map-branch-trigger]");
        if (branchTriggers) {
            branchTriggers.forEach(branchTrigger => {
                branchTrigger.addEventListener("click", (e) => this._selectMapTab(e.target.parentElement));
                branchTrigger.addEventListener("keyup", (e) => this._selectMapTab(e.target));
                branchTrigger.addEventListener("keydown", (e) => {
                    const key = e.code || e.keyCode;
                    if ([KEYCODES.HOME, KEYCODES.END, "Home", "End"].includes(key)) {
                        e.preventDefault();
                    }
                });
            });
        }
    }

    _selectMapTab(target) {
        this.rootElement.querySelectorAll('[data-map-tab]').forEach(element => element.classList.add("hidden@all"));
        this.rootElement.querySelector('[data-map-tab="' + target.getAttribute("data-map-branch-trigger") + '"]').classList.remove("hidden@all");

        var brandItems = this.rootElement.querySelectorAll(".swrz-o-locations__brand-nav-item");
        brandItems.forEach(brandItem => brandItem.classList.remove("swrz-o-locations__brand-nav-item--selected"));

        target.classList.add("swrz-o-locations__brand-nav-item--selected");

        /**
         * Initialize the active tab-container.
         * @type {NucleusOTabContainer}
         */
        let tabContainer = Factory.createComponent(this.rootElement.querySelector('[data-map-tab="' + target.getAttribute("data-map-branch-trigger") + '"]'));
        tabContainer.initContainer();
    }
}

Factory.registerComponent(SchwarzOLocations);
