import nunjucks from "nunjucks";
import CoreExtension from "./extension/core-extension";
import SchemaExtension from "./extension/schema-extension";
import GtmExtension from "./extension/gtm-extension";
import DebugExtension from "./extension/debug-extension";
import TwigCompatExtension from "./extension/twig-compat-extension";
import NucleusLoader from "./loader/nucleus-loader";
import SymfonyCompatExtension from "./extension/symfony-compat-extension";
import {Config} from "@nucleus/core/src/_config";

window.nunjucksPrecompiled = window.nunjucksPrecompiled || {};

class _NunjucksWrapper {

    constructor() {
        this._loadingPromise = Promise.all([
            this._loadAssetManifest(),
            this._loadNucleusManifest()
        ]);

        this._environment = new nunjucks.Environment(new NucleusLoader(this));
        this._environment.addGlobal("clientSideRendering", true);

        this._extensions = [];
        this._extensions.push(new CoreExtension(this));
        this._extensions.push(new SchemaExtension(this));
        this._extensions.push(new DebugExtension(this));
        this._extensions.push(new GtmExtension(this));
        this._extensions.push(new TwigCompatExtension(this));
        this._extensions.push(new SymfonyCompatExtension(this));
    }

    get loadingPromise() {
        return this._loadingPromise;
    }

    get environment() {
        return this._environment;
    }

    get assetManifest() {
        return this._assetManifest;
    }

    get nucleusManifest() {
        return this._nucleusManifest;
    }

    _loadAssetManifest() {
        return fetch(Config.assetManifestPath)
            .then(response => response.json())
            .then(assetManifest => this._assetManifest = assetManifest);
    }

    _loadNucleusManifest() {
        return fetch(Config.nucleusManifestPath)
            .then(response => response.json())
            .then(nucleusManifest => {
                this._nucleusManifest = nucleusManifest;
            });
    }

    renderComponent(data) {
        return this._loadingPromise.then(() => {
            return new Promise((resolve, reject) => {
                const componentParts = data.component.split("/");
                const namespace = componentParts[0];
                const component = componentParts[1];

                const template = this.environment.getTemplate("@" + namespace + "/" + component + ".twig");
                template.getExported(function (error, exportedObjects) {
                    if (error) {
                        reject(error);
                        return
                    }
                    let renderedHTML = exportedObjects.render(data);
                    resolve(renderedHTML);
                });
            });
        });
    }
}

export const NunjucksWrapper = new _NunjucksWrapper();