import Swiper from "swiper";
import { Component, Factory } from "@nucleus/core/src/_core";
import { BREAKPOINTS, FOCUSABLE_ELEMENTS } from "@nucleus/core/src/_constants";

export default class NucleusMThemeSlider extends Component {
    static get rootClassName() {
        return "nuc-m-theme-slider";
    }

    /**
     * Creates an instance of NucleusMThemeSlider.
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);

        this._addSelectors({
            container: ".nuc-m-theme-slider__container",
            buttonPrev: ".nuc-m-theme-slider__button-prev",
            buttonNext: ".nuc-m-theme-slider__button-next",
            item: ".nuc-m-theme-slider__item",
            focusable: FOCUSABLE_ELEMENTS.join(", "),
            negativeTabIndex: `[tabindex^="-"]`
        });

        this._addElements({
            wrapper: "nuc-m-theme-slider__wrapper",
            notification: "nuc-m-theme-slider__notification",
            item: "nuc-m-theme-slider__item",
            items: this._elements.root.querySelectorAll(this._selectors.item),
            container: rootElement.querySelector(this._selectors.container),
            buttonPrev: rootElement.querySelector(this._selectors.buttonPrev),
            buttonNext: rootElement.querySelector(this._selectors.buttonNext),
            focusable: rootElement.querySelectorAll(this._selectors.focusable)
        });

        this._addModifiers({
            item: {
                visible: "nuc-m-theme-slider__item--visible"
            }
        });

        this._swiper = new Swiper(this._elements.container, {
            on: {
                init: () => {
                    this._handleTabIndex();
                },
                resize: () => {
                    this._handleTabIndex();
                },
                slideChange: () => {
                    this._handleTabIndex();
                }
            },
            spaceBetween: 8,
            simulateTouch: false,
            wrapperClass: this._elements.wrapper,
            slideClass: this._elements.item,
            slideVisibleClass: this._modifiers.item.visible,
            keyboard: {
                enabled: true
            },
            navigation: {
                nextEl: this._elements.buttonNext,
                prevEl: this._elements.buttonPrev,
                disabledClass: "hidden@all"
            },
            a11y: {
                notificationClass: this._elements.notification,
                prevSlideMessage: this._elements.root.dataset.ariaMessagePrev,
                nextSlideMessage: this._elements.root.dataset.ariaMessageNext,
                firstSlideMessage: this._elements.root.dataset.ariaMessageFirst,
                lastSlideMessage: this._elements.root.dataset.ariaMessageLast
            },
            slidesPerView: "auto",
            watchSlidesVisibility: true,
            breakpoints: {
                [BREAKPOINTS["xs"]]: {
                    slidesPerGroup: 2
                },
                [BREAKPOINTS["md"]]: {
                    slidesPerGroup: 3
                },
                [BREAKPOINTS["lg"]]: {
                    slidesPerGroup: 4
                }
            }
        });
    }

    /**
     * allow tabbing only on visible items
     * @private
     */
    _handleTabIndex() {
        this._elements.root.querySelectorAll(this._selectors.item).forEach((slide, index) => {
            if (slide.classList.contains(this._modifiers.item.visible)) {
                slide.querySelectorAll(this._selectors.negativeTabIndex).forEach((focusable) => {
                    focusable.removeAttribute("tabindex");
                });
            } else {
                slide.querySelectorAll(this._selectors.focusable).forEach((focusable) => {
                    focusable.tabIndex = -1;
                });
            }
        });
    }
}

Factory.registerComponent(NucleusMThemeSlider);
