import { Component, Factory } from "@nucleus/core/src/_core";
import Swiper from "swiper";
import * as mergeOptions from "merge-options";
import { BREAKPOINTS, FOCUSABLE_ELEMENTS } from "@nucleus/core/src/_constants";

export default class NucleusMSlider extends Component {
    static get rootClassName() {
        return "nuc-m-slider";
    }

    /**
     * Creates an instance of NucleusMSlider.
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);

        /**
         * @type {Element}
         * @private
         */
        this._container = rootElement.querySelector(".nuc-m-slider__container");

        /**
         * @type {Element}
         * @private
         */
        this._buttonPrev = rootElement.querySelector(".nuc-m-slider__button-prev");

        /**
         * @type {Element}
         * @private
         */
        this._buttonNext = rootElement.querySelector(".nuc-m-slider__button-next");

        /**
         * @type {Element}
         * @private
         */
        this._focusable = rootElement.querySelectorAll(FOCUSABLE_ELEMENTS.join(", "));

        this._modifiers = {
            item: {
                visible: "nuc-m-slider__item--visible",
                duplicate: "nuc-m-slider__item--duplicate"
            }
        };

        /**
         * @type {Swiper}
         * @private
         */
        this._swiper = new Swiper(this._container, {
            on: {
                init: () => {
                    this._handleTabIndex();
                },
                resize: () => {
                    this._handleTabIndex();
                },
                slideChange: () => {
                    this._handleTabIndex();
                },
                loopFix: () => {
                    this._handleTabIndex();
                }
            },
            autoplay: rootElement.dataset.hasAutoplay !== void 0 ?
                (!!rootElement.dataset.delay ? {delay: Number(rootElement.dataset.delay)} : true) : false,
            pagination: {
                el: ".nuc-m-slider__pagination",
                bulletClass: "nuc-m-slider__bullet",
                bulletActiveClass: "nuc-m-slider__bullet--active",
                clickable: true,
                clickableClass: "nuc-o-stage-slider__pagination--clickable"
            },
            spaceBetween: 16,
            loop: rootElement.dataset.isLoop !== void 0,
            simulateTouch: false,
            wrapperClass: "nuc-m-slider__wrapper",
            slideClass: "nuc-m-slider__item",
            slideVisibleClass: this._modifiers.item.visible,
            slideDuplicateClass: this._modifiers.item.duplicate,
            keyboard: {
                enabled: true
            },
            navigation: {
                nextEl: this._buttonNext,
                prevEl: this._buttonPrev,
                disabledClass: "hidden@all"
            },
            a11y: {
                prevSlideMessage: rootElement.dataset.ariaMessagePrev,
                nextSlideMessage: rootElement.dataset.ariaMessageNext,
                firstSlideMessage: rootElement.dataset.ariaMessageFirst,
                lastSlideMessage: rootElement.dataset.ariaMessageLast
            },
            breakpoints: mergeOptions(
                this._buildResponsiveConfig(
                    "slidesPerView",
                    rootElement.dataset.slidesPerView
                ),
                this._buildResponsiveConfig(
                    "slidesPerGroup",
                    rootElement.dataset.slidesPerGroup
                )
            ),
            watchSlidesVisibility: true
        });

        // ~ For accessibility the slider has to stop / start on mouseenter / leave, focus / blur
        if (rootElement.dataset.hasAutoplay !== void 0) {
            rootElement.addEventListener("mouseenter", () => {
                this._swiper.autoplay.stop();
            });
            rootElement.addEventListener("mouseleave", () => {
                this._swiper.autoplay.start();
            });
            this._focusable.forEach((focusable) => {
                focusable.addEventListener("focus", () => {
                    this._swiper.autoplay.stop();
                });
                focusable.addEventListener("blur", () => {
                    this._swiper.autoplay.start();
                });
            });
        }

        if (rootElement.dataset.isLoop !== void 0) {
            this._initDuplicateSlides();
        }
    }

    /**
     * allow tabbing only on visible items
     * @private
     */
    _handleTabIndex() {
        this.rootElement.querySelectorAll(".nuc-m-slider__item").forEach((slide, index) => {
            if (slide.classList.contains(this._modifiers.item.visible)) {
                slide.querySelectorAll("[tabindex^=\"-\"]").forEach((focusable) => {
                    focusable.removeAttribute("tabindex");
                });
            } else {
                slide.querySelectorAll(FOCUSABLE_ELEMENTS.join(", ")).forEach((focusable) => {
                    focusable.tabIndex = -1;
                });
            }
        });
    }

    /**
     * build config object for responsive slider settings
     * @param name
     * @param data
     * @private
     */
    _buildResponsiveConfig(name, data) {
        let obj = {},
            config = JSON.parse(data) || "";
        for (let prop in config) {
            obj[BREAKPOINTS[prop]] = {};
            obj[BREAKPOINTS[prop]][name] = config[prop];
        }
        return obj;
    }

    /**
     * initialize duplicated slides
     * @private
     */
    _initDuplicateSlides() {
        let duplicate = this.rootElement.querySelectorAll(".nuc-m-slider__item--duplicate");

        duplicate.forEach(item => Factory.initAll(item));
    }
}

Factory.registerComponent(NucleusMSlider);
