/**
 * Contains helper methods for event handling.
 */
export default class EventUtils {
    /**
     * Debouncing, executes the function if there was no new event in $wait milliseconds
     *
     * @param {function} func           - The function that should be fired with a debounce
     * @param {number}  [wait=500]      - Time until the function will be fired again
     * @param {Object}  [context=this]    - The context of the fired function
     * @returns {function}
     */
    static debounce(func, wait = 500, context) {
        var timeout;
        wait = (wait === undefined) ? 500 : wait;
        return function debounceReturn() {
            context = (context === undefined) ? this : context;
            var args = arguments;
            var later = function later() {
                timeout = null;
                func.apply(context, args);
            };
            clearTimeout(timeout);
            timeout = setTimeout(later, wait);
        };
    }

    /**
     * In case of a "storm of events", this executes once every threshold
     *
     * @param {function} func               - The function that should be fired with a throttle
     * @param {number}  [threshold=250]     - Time until the function will be fired again
     * @param {Object}  [context=this]        - The context of the fired function
     * @returns {function}
     */
    static throttle(func, threshold, context) {
        threshold = (threshold === undefined) ? 250 : threshold;
        var last;

        return function throttleReturn() {
            context = (context === undefined) ? this : context;
            var now = +new Date();
            var args = arguments;
            last = context.throttlelast;
            if (!(last && now < last + threshold)) {
                context.throttlelast = now;
                func.apply(context, args);
            }
        };
    }

    /**
     * A function which you only want to run once
     *
     * @param {function} func               - The function that should be fired only once
     * @param {Object} [context=this]     - The context of the fired function
     * @returns {function}
     */
    static once(func, context) {
        var result;
        return function onceInner() {
            if (func) {
                context = (context === undefined) ? this : context;
                result = func.apply(context, arguments);
                func = null;
            }

            return result;
        };
    }
}
