import "@nucleus/polyfill/src/_polyfill";
import { Component, Factory, Subject } from "@nucleus/core/src/_core";
import EventUtils from "@nucleus/core/src/_eventUtils";

export default class SchwarzGroupMGenderDrawer extends Component {

    /**
     * @inheritDoc
     */
    static get rootClassName() {
        return "scgr-m-gender-drawer";
    }

    /**
     * Creates an instance of SchwarzGroupMGenderDrawer.
     *
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);

        /**
         * @type {boolean}
         * @private
         */
        this._isVisible = false;
        this._height = this.rootElement.getBoundingClientRect().height;
        this._offset = window.pageYOffset;

        /**
         * @type {NucleusAButton}
         * @private
         */
        this._closeButton = Factory.createComponent(rootElement.querySelector(".scgr-m-gender-drawer__close-button"));
        this._closeButton.clickSubject.subscribe(() => {
            this._isVisible = false;
            this.hide();
        });

        window.addEventListener("scroll", EventUtils.debounce((event) => this._handleWindowScroll(), 150));

        var observer = new IntersectionObserver((entries) => {
            if(entries[0].isIntersecting && !this._isVisible) {
                this._height = this.rootElement.getBoundingClientRect().height;
                window.scrollTo(0, window.pageYOffset + this._height);
                this._isVisible = true;
            } else if (window.getComputedStyle(entries[0].target).display === "none") {
                this._isVisible = false;
                window.scrollTo(0, this._offset - this._height);
            }
        }, {threshold: [0]});

        observer.observe(this.rootElement);
    }

    _handleWindowScroll() {
        this._offset = window.pageYOffset;
    }
}

Factory.registerComponent(SchwarzGroupMGenderDrawer);
