import Extension from "./extension";
import CoreExtension from "./core-extension";

/**
 * Keep this extension of nunjucks in sync with the Twig version of it in Nucleus\CoreBundle\Twig\GtmExtension.
 */
export default class GtmExtension extends Extension {

    constructor(nunjucksWrapper) {
        super(nunjucksWrapper);

        nunjucksWrapper.environment.addFilter("merge_gtm_data", GtmExtension.mergeGtmData);
    }

    static mergeGtmData(data, gtmData) {
        let htmlAttributes = {};

        for (let key in gtmData) {
            htmlAttributes["data-gtm-" + key] = gtmData[key];
        }

        return CoreExtension.mergeData(data, {
            htmlAttributes: htmlAttributes
        });
    }
}
