import "@nucleus/polyfill/src/_polyfill";
import { Factory, Subject } from "@nucleus/core/src/_core";
import NucleusAAnchor from "@nucleus/a-anchor";

export default class NucleusMSvgMapMarker extends NucleusAAnchor {

    /**
     * @inheritDoc
     */
    static get rootClassName() {
        return "nuc-m-svg-map-marker";
    }

    /**
     * Creates an instance of NucleusMSvgMapMarker.
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);

        this._highlighted = false;
    }

    /**
     * Get reference identifier.
     * @return {string}
     */
    get mapItemReferenceId() {
        return this.rootElement.dataset.mapItemReferenceId;
    }

    /**
     * Get markers highlighted state.
     * @return {boolean}
     */
    get highlighted() {
        return this._highlighted;
    }

    /**
     * Add or remove marker highlight state.
     * @param {boolean} highlighted
     */
    set highlighted(highlighted) {
        this._highlighted = highlighted;
    }

    /**
     * Set marker's position on map via inline css.
     * @param {object} position
     * @param {string} position.left
     * @param {string} position.top
     */
    set position(position) {
        this.rootElement.style.top = position.top;
        this.rootElement.style.left = position.left;
    }
}

Factory.registerComponent(NucleusMSvgMapMarker);