import "@nucleus/polyfill/src/_polyfill";
import { Component, Factory, Subject } from "@nucleus/core/src/_core";
import NucleusAButton from "@nucleus/a-button";

export default class NucleusMAccordionItem extends Component {

    static get rootClassName() {
        return "nuc-m-accordion-item";
    }

    /**
     * Creates an instance of NucleusMAccordionItem.
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);

        this._modifiers = {
            root: {
                open: "nuc-m-accordion-item--open"
            }
        };

        this._attributes = {
            trigger: {
                ariaExpanded: "aria-expanded"
            }
        };

        /**
         * @type {Subject}
         * @private
         */
        this._stateSubject = new Subject();

        /**
         * @type {NucleusAButton}
         */
        this._trigger = Factory.createComponent(this.querySelector(".nuc-m-accordion-item__trigger"));
        this._trigger.clickSubject.subscribe(() => this._onTriggerClick());

        this._onHashChange();

        window.addEventListener("hashchange", () => this._onHashChange());
    }

    /**
     * Callback for the accordion-item-button click event.
     * @private
     */
    _onHashChange() {
        if (window.location.hash.length > 0) {
            let hash = window.location.hash.substring(1);
            let contentWithAnchor = this.querySelector(window.location.hash);

            if (contentWithAnchor || hash === this.rootElement.id) {
                this.state = NucleusMAccordionItem.States.OPEN;
                this.rootElement.scrollIntoView();
            }
        }
    }

    /**
     * Callback for the accordion-item-button click event.
     * @private
     */
    _onTriggerClick() {
        if (this.state === NucleusMAccordionItem.States.OPEN) {
            this.state = NucleusMAccordionItem.States.CLOSE;
        } else {
            this.state = NucleusMAccordionItem.States.OPEN;
        }
    }

    /**
     * Subject for state changes
     *
     * @return {Subject}
     */
    get stateSubject() {
        return this._stateSubject;
    }

    /**
     * Returns the state of the accordion item
     *
     * @return {number}
     */
    get state() {
        if (this.hasClass(this._modifiers.root.open)) {
            return NucleusMAccordionItem.States.OPEN;
        } else {
            return NucleusMAccordionItem.States.CLOSE;
        }
    }

    /**
     * Sets the state of the component to open or closed, based on the current state.
     */
    set state(state) {
        if (state === NucleusMAccordionItem.States.OPEN) {
            this.addClass(this._modifiers.root.open);
            this._trigger.setAttribute(this._attributes.trigger.ariaExpanded, true);
        }
        if (state === NucleusMAccordionItem.States.CLOSE) {
            this.removeClass(this._modifiers.root.open);
            this._trigger.setAttribute(this._attributes.trigger.ariaExpanded, false);
        }
        this._stateSubject.notify(this);
    }

    /**
     * Enum to represent the state of an accordion item
     *
     * @type {Readonly<{OPEN: number, CLOSE: number, }>}
     */
    static States = Object.freeze({
        CLOSE: 0,
        OPEN: 1
    });
}

Factory.registerComponent(NucleusMAccordionItem);