import {Config} from "@nucleus/core/src/_config";

class _i18n {
    constructor() {
        /**
         * @type {Promise<any>}
         * @private
         */
        this._loadingPromise = this._loadTranslations();
    }

    /**
     * Loads the translations.
     *
     * @returns {Promise<any>}
     * @private
     */
    _loadTranslations() {
        if (Config.translationsJsonPath) {
            return fetch(Config.translationsJsonPath)
                .then(response => {
                    if (!response.ok) {
                        throw new Error(`HTTP status ${response.status} ${response.statusText}`);
                    }

                    return response.json();
                })
                .catch(error => {
                    console.error(`[nucleus] Error during loading translations (${error})`);
                });
        } else {
            console.error("[nucleus] No path for translations configured");
        }
    }

    /**
     * Translates a given translation
     * label
     * @param label
     * @returns {Promise<string>}
     */
    async translate(label) {
        let translations = await this._loadingPromise || {};
        let keys = label.split(".");

        while (keys.length && (translations = translations[keys.shift()]));

        if (translations === undefined || typeof translations !== "string") {
            console.warn(`[nucleus] Translation missing: ${label}`);
            return `[Translation missing: ${label}]`;
        }

        return translations;
    }

    pluralize(stringTemplate, values, number) {
        const pluralTemplates = stringTemplate.split("|");
        if (pluralTemplates.length < 3 || typeof number !== "number") {
            // no templates given for plural, always take the one given template
            return this._fillStringTemplate(stringTemplate.trim(), values);
        }
        return this._applyPluralizationRules(pluralTemplates, values, number);
    }

    _fillStringTemplate(stringTemplate, values) {
        Object.keys(values).forEach(key => {
            const regex = new RegExp(`{{${key}}}`, "g");
            stringTemplate = stringTemplate.replace(regex, values[key]);
        });
        return stringTemplate;
    }

    _applyPluralizationRules(pluralTemplates, values, number) {
        if (number < 2) {
            // first two templates are for 0 and 1
            return this._fillStringTemplate(pluralTemplates[number].trim(), values);
        }
        // Remaining templates are for different plurals
        // These might be language specific, so once we need to support such languages, add implementations for those here
        // For now, we just have one plural form for all numbers bigger than one
        return this._fillStringTemplate(pluralTemplates[2].trim(), values);
    }
}

export const i18n = new _i18n();
