import "@nucleus/polyfill/src/_polyfill";
import {Factory, Subject} from '@nucleus/core/src/_core';
import NucleusAButton from "@nucleus/a-button";
import {KEYCODES} from "@nucleus/core/src/_constants";


export default class NucleusMTabItem extends NucleusAButton {

    static get rootClassName() {
        return "nuc-m-tab-item";
    }

    /**
     * Creates an instance of NucleusMTabItem.
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);

        this._modifiers = {
            root: {
                selected: "nuc-m-tab-item--selected",
                previous: "nuc-m-tab-item--previous",
                next: "nuc-m-tab-item--next"
            }
        };

        this._attributes = {
            root: {
                ariaSelected: "aria-selected",
                ariaControls: "aria-controls",
                tabindex: "tabindex"
            }
        };

        /**
         * @type {Subject}
         * @private
         */
        this._stateSubject = new Subject();

        this._clickSubject.subscribe(() => {
            this.state = NucleusMTabItem.States.SELECTED;
        });

        /**
         * @type {Subject}
         * @private
         */
        this._keyUpSubject = new Subject();

        this.rootElement.addEventListener("keyup", (event) => this._keyUpSubject.notify(this, event));
        this.rootElement.addEventListener("keydown", (event) => {
            const key = event.code || event.keyCode;
            if ([KEYCODES.HOME, KEYCODES.END, "Home", "End"].includes(key)) {
                event.preventDefault();
            }
        });
    }

    /**
     * Subject for key up event.
     * @return {Subject}
     */
    get keyUpSubject() {
        return this._keyUpSubject;
    }

    /**
     * Subject for state changes. This subject triggers a event every time the state of the tab item is set, even is the
     * state is not changed.
     * @return {Subject}
     */
    get stateSubject() {
        return this._stateSubject;
    }

    /**
     * Returns the state of the tab item.
     * @return {number}
     */
    get state() {
        if (this.rootElement.classList.contains(this._modifiers.root.selected)) {
            return NucleusMTabItem.States.SELECTED;
        } else if (this.rootElement.classList.contains(this._modifiers.root.previous)) {
            return NucleusMTabItem.States.PREVIOUS;
        } else if (this.rootElement.classList.contains(this._modifiers.root.next)) {
            return NucleusMTabItem.States.NEXT;
        } else {
            return NucleusMTabItem.States.NONE;
        }
    }

    /**
     * Sets the state of the tab item.
     * @param {number} state
     */
    set state(state) {
        this.rootElement.classList.remove(this._modifiers.root.selected);
        this.rootElement.classList.remove(this._modifiers.root.previous);
        this.rootElement.classList.remove(this._modifiers.root.next);
        this.rootElement.setAttribute(this._attributes.root.ariaSelected, false);
        this.rootElement.setAttribute(this._attributes.root.tabindex, -1);

        if (state === NucleusMTabItem.States.SELECTED) {
            this.rootElement.classList.add(this._modifiers.root.selected);
            this.rootElement.setAttribute(this._attributes.root.ariaSelected, true);
            this.rootElement.removeAttribute(this._attributes.root.tabindex);
        } else if (state === NucleusMTabItem.States.PREVIOUS) {
            this.rootElement.classList.add(this._modifiers.root.previous);
        } else if (state === NucleusMTabItem.States.NEXT) {
            this.rootElement.classList.add(this._modifiers.root.next);
        }

        this._stateSubject.notify(this);
    }

    /**
     * Returns the text of the tab item.
     * @return {String}
     */
    get text() {
        return this.rootElement.innerText;
    }

    /**
     * Sets the text of the tab item.
     * @param text
     * @type {String}
     */
    set text(text) {
        this.rootElement.innerText = text;
    }

    linkToContent(tabContent) {
        this.rootElement.setAttribute(this._attributes.root.ariaControls, tabContent.rootId);
    }

    /**
     * Enum to represent the state of a tab item.
     * @type {Readonly<{SELECTED: number, NEXT: number, NONE: number, PREVIOUS: number}>}
     */
    static States = Object.freeze({
        SELECTED: 1,
        NEXT: 2,
        PREVIOUS: 3,
        NONE: 4
    });
}

Factory.registerComponent(NucleusMTabItem);
