import "@nucleus/polyfill/src/_polyfill";
import { Component, Factory } from "@nucleus/core/src/_core";

export default class NucleusAStageOverlay extends Component {
    /**
     * @inheritDoc
     */
    static get rootClassName() {
        return "nuc-a-stage-overlay";
    }

    /**
     * Creates an instance of NucleusAStageOverlay.
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);
    }

    /**
     * Sets element's height equal to parent component`s stage height.
     * @param height
     */
    set height(height) {
        this.rootElement.style.height = height;
        this.show();
    }
}

Factory.registerComponent(NucleusAStageOverlay);
