import "@nucleus/polyfill/src/_polyfill";
import {AbstractModuleCore, Factory} from "@nucleus/core/src/_core";

export default class NucleusECollapse {
    /**
     * Creates an instance of NucleusECollapse.
     *
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        this._attributes = {
            "triggerElement": {
                ariaExpanded: "aria-expanded",
                ariaControls: "aria-controls"
            }
        };

        this._collapseAttributes = JSON.parse(rootElement.dataset.nucleusECollapse);

        /**
         * @type {NucleusAButton|NucleusAAnchor}
         * @private
         */
        this._triggerElement = Factory.createComponent(rootElement);
        this._triggerElement.clickSubject.subscribe((el, event) => {
            event.preventDefault();
            if (this._collapseAttributes.idGroup) {
                this._hideGroupItems();
            }
            this._toggleCollapse();
        });
        this._triggerElement.setAttribute(
            this._attributes.triggerElement.ariaControls,
            this._collapseAttributes.idTarget
        );

        /**
         * @type {Component}
         * @private
         */
        this._targetContainer = Factory.createComponent(
            document.querySelector("#" + this._collapseAttributes.idTarget)
        );
        this._targetContainer.visibilitySubject.subscribe((event, state) => {
            this._triggerElement.setAttribute(this._attributes.triggerElement.ariaExpanded, state);
        });
    }

    /**
     * Shows or hides the target container.
     * @private
     */
    _toggleCollapse() {
        if (this._triggerElement.rootElement.getAttribute(this._attributes.triggerElement.ariaExpanded) === "false") {
            this._targetContainer.show();
        } else {
            this._targetContainer.hide();
        }
    }

    /**
     * Hide other elements, assigned to same group.
     * @private
     */
    _hideGroupItems() {
        /**
         * @type {NucleusAButton}
         */
        this._triggerGroup = Factory.createComponents(document.querySelectorAll("[data-nucleus-e-collapse]"));
        this._triggerGroup.forEach(item => {
            let collapseAttributes = JSON.parse(item.rootElement.dataset.nucleusECollapse);
            let isGroupSibling =
                collapseAttributes.idTarget !== this._collapseAttributes.idTarget &&
                collapseAttributes.idGroup === this._collapseAttributes.idGroup;

            if (isGroupSibling) {
                let targetContainer = Factory.createComponent(
                    document.querySelector("#" + collapseAttributes.idTarget)
                );
                targetContainer.hide();
            }
        });
    }
}

class _CollapseCore extends AbstractModuleCore {
    /**
     * @inheritDoc
     */
    get rootSelector() {
        return "[data-nucleus-e-collapse]";
    }

    /**
     * @inheritDoc
     */
    createModule(rootElement) {
        if (rootElement) {
            if (rootElement._nucleusCollapseInstance) {
                return rootElement._nucleusCollapseInstance;
            }

            if (rootElement.hasAttribute("data-nucleus-e-collapse")) {
                let instance = new NucleusECollapse(rootElement);
                rootElement._nucleusCollapseInstance = instance;
                return instance;
            }
        }

        return null;
    }
}

const CollapseCore = new _CollapseCore();
Factory.registerModuleCore(CollapseCore);
