import "@nucleus/polyfill/src/_polyfill";
import NucleusMAccordionItem from "@nucleus/m-accordion-item";
import {Component, Factory} from "@nucleus/core/src/_core";

/**
 * @public
 * @class
 */
export default class NucleusOAccordionContainer extends Component {

    static get rootClassName() {
        return "nuc-o-accordion-container";
    }

    /**
     * Creates an instance of NucleusOAccordionContainer.
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);

        this._modifiers = {
            root: {
                multiple: "nuc-o-accordion-container--multiple"
            }
        }

        /**
         * @type {NucleusMAccordionItem[]}
         */
        this._accordionItems = Factory.createComponents(this.querySelectorAll(NucleusMAccordionItem.rootSelector));
        this._accordionItems.forEach(accordionItem => accordionItem.stateSubject.subscribe(currentAccordionItem => {
            if (currentAccordionItem.state === NucleusMAccordionItem.States.OPEN) {
                this._updateAccordionItems(currentAccordionItem);
            }
        }));
    }

    /**
     * update accordion items
     * @private
     */
    _updateAccordionItems(selectedAccordionItem) {
        if (!this.hasClass(this._modifiers.root.multiple)){
            this._accordionItems.forEach(accordionItem => {
                if (accordionItem !== selectedAccordionItem) {
                    accordionItem.state = NucleusMAccordionItem.States.CLOSE;
                }
            });
        }
    }
}

Factory.registerComponent(NucleusOAccordionContainer);