
import "@nucleus/polyfill/src/_polyfill";
import { Component, Factory } from "@nucleus/core/src/_core";
import NucleusAStageOverlay from "@nucleus/a-stage-overlay";
import NucleusMPicture from "@nucleus/m-picture";
import EventUtils from "@nucleus/core/src/_eventUtils";

export default class NucleusOStage extends Component {
    /**
     * @inheritDoc
     */
    static get rootClassName() {
        return "nuc-o-stage";
    }

    /**
     * Creates an instance of NucleusOStage.
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);

        /**
         * @type {Component}
         * @private
         */
        this._overlayContainer = Factory.createComponent(this.querySelector(".nuc-o-stage__overlay-container"));

        if (this._overlayContainer) {
            /**
             * @type {HTMLElement}
             * @private
             */
            this._mediaSizeContainer = rootElement.querySelector(".nuc-o-stage__media-size-container");

            /**
             * @type {NucleusAStageOverlay[]}
             * @private
             */
            this._overlays = Factory.createComponents(this._overlayContainer.querySelectorAll(NucleusAStageOverlay.rootSelector));

            /**
             * @type {ResizeObserver}
             */
            let stageResizeObserver = new ResizeObserver(() => this._updateOverlayHeight());
            stageResizeObserver.observe(this._mediaSizeContainer);

            /**
             * @type {ResizeObserver}
             */
            let overlayResizeObserver = new ResizeObserver(() => this._updateStageHeight());
            this._overlays.forEach(overlay => overlayResizeObserver.observe(overlay.rootElement));
        }

        /**
         * @type {Component[]}
         * @private
         */
        this._media = Factory.createComponents(this.querySelectorAll(".nuc-o-stage__media")); // selectorAll is necessary for responsive media

        if ("isParallax" in this.rootElement.dataset) {

            /**
             * The request ID of the animation frame.
             * @type {number}
             * @private
             */
            this._animationRequestID = null;

            /**
             * @type {IntersectionObserver}
             */
            let observer = new IntersectionObserver((entries, observer) => {
                entries.forEach(entry => {
                    if (entry.isIntersecting) {
                        this._animationRequestID = requestAnimationFrame(this._animateMedia);
                    } else {
                        if (this._animationRequestID) {
                            cancelAnimationFrame(this._animationRequestID);
                            this._animationRequestID = null;
                        }
                    }
                });
            });

            observer.observe(this.rootElement);
        }
    }

    /**
     * Sets the height of the overlays to the height of the picture.
     * @private
     */
    _updateOverlayHeight() {
        this._overlayContainer.rootElement.style.paddingTop = this._mediaSizeContainer.offsetHeight + "px";
        this._overlays.forEach((overlay) => {
            overlay.height = this._mediaSizeContainer.offsetHeight + "px";
        });
    }

    /**
     * Sets the height of the stage by setting a padding for the largest overlay below the stage.
     * @private
     */
    _updateStageHeight() {
        let heights = this._overlays
            .filter(overlay => window.getComputedStyle(overlay.rootElement).getPropertyValue("position") === "relative")
            .map(overlay => overlay.rootElement.offsetHeight);
        this.rootElement.style.paddingBottom = Math.max(0, ...heights) + "px";
    }

    /**
     * Animate media element.
     * @private
     */
    _animateMedia = () => {
        let currentAbsolutePosition = this.getAbsolutePositionTop(this.rootElement);
        let isVisibleTop = currentAbsolutePosition <= this._offSetTop && currentAbsolutePosition >= (this.rootElement.offsetHeight * -1);
        let isVisibleBottom = currentAbsolutePosition >= window.innerHeight - this.rootElement.offsetHeight - this._offSetBottom && currentAbsolutePosition <= window.innerHeight;

        if (isVisibleTop) {
            let position = ((currentAbsolutePosition - this._offSetTop) * -1) / 1.2;
            this._media.forEach(component => component.rootElement.style.top = position + "px");
        } else if (isVisibleBottom) {
            let position = ((this.rootElement.offsetHeight - (window.innerHeight - this.getAbsolutePositionTop(this.rootElement) - this._offSetBottom)) * -1) / 1.2;
            this._media.forEach(component => component.rootElement.style.top = position + "px");
        } else {
            this._media.forEach(component => component.rootElement.style.top = "0px");
        }
        this._animationRequestID = requestAnimationFrame(this._animateMedia);
    }

    /**
     * Return offset top value.
     * @returns {number}
     * @protected
     */
    get _offSetTop() {
        return 0;
    }

    /**
     * Return offset bottom value.
     * @returns {number}
     * @protected
     */
    get _offSetBottom() {
        return 0;
    }
}

Factory.registerComponent(NucleusOStage);
