import NucleusEAnimationPreset from "@nucleus/e-animation-preset";
import NucleusEAnimationItem from "@nucleus/e-animation-item";

/**
 * Loads the tracking code for the monotype usage tracking.
 * @param {string} userId
 * @param {string[]} fontIds
 */
function addMonotypeTracking(userId, fontIds) {
    const trackingElement = document.createElement("link");
    trackingElement.type = "text/css";
    trackingElement.rel = "stylesheet";
    trackingElement.href = "https://fast.fonts.net/lt/1.css?apiType=css&c=" + userId + "&fontids=" + fontIds.join(",");
    document.head.appendChild(trackingElement);
}

addMonotypeTracking("d254f134-7e09-4042-a310-16a4c838ff7f", [
    "1488974", // Neue Helvetica W01 47 Condensed Light
    "1488998"  // Neue Helvetica W01 67 Condensed Medium
]);

// Add timing functions for the animation extension.
NucleusEAnimationItem.addTimingFunction("schwarz-ease-out", [0.61, 1, 0.88, 1]);
NucleusEAnimationItem.addTimingFunction("schwarz-ease-in", [0.55, 0, 1, 0.45]);
NucleusEAnimationItem.addTimingFunction("schwarz-easeInOutCubic", [0.68, -0.6, 0.32, 1.6]);

// Add duration times for the animation extension.
NucleusEAnimationItem.addDuration("schwarz-duration-10", "200ms");
NucleusEAnimationItem.addDuration("schwarz-duration-20", "400ms");
NucleusEAnimationItem.addDuration("schwarz-duration-30", "600ms");
NucleusEAnimationItem.addDuration("schwarz-duration-40", "800ms");
NucleusEAnimationItem.addDuration("schwarz-duration-50", "1200ms");
NucleusEAnimationItem.addDuration("schwarz-duration-60", "1400ms");
NucleusEAnimationItem.addDuration("schwarz-duration-70", "1600ms");
NucleusEAnimationItem.addDuration("schwarz-duration-80", "1800ms");
NucleusEAnimationItem.addDuration("schwarz-duration-90", "2000ms");
NucleusEAnimationItem.addDuration("schwarz-duration-100", "2200ms");
NucleusEAnimationItem.addDuration("schwarz-duration-110", "2400ms");

// Add global animations for the animation extension.
NucleusEAnimationPreset.addPreset("fade-in", [
    {
        module: "nucleus/e-animation-item",
        animation: "opacity schwarz-duration-40 schwarz-ease-out",
        startValue: "0.54"
    }
]);
NucleusEAnimationPreset.addPreset("scale", [
    {
        module: "nucleus/e-animation-item",
        animation: "transform schwarz-duration-50 schwarz-easeInOutCubic",
        startValue: "scale(1.2)"
    }
]);
NucleusEAnimationPreset.addPreset("to-right", [
    {
        module: "nucleus/e-animation-item",
        animation: "transform schwarz-duration-20 schwarz-ease-out",
        startValue: "translateX(-60px)"
    }
]);
NucleusEAnimationPreset.addPreset("letter-spacing", [
    {
        module: "nucleus/e-animation-item",
        animation: "letter-spacing schwarz-duration-20 schwarz-ease-in",
        startValue: "5px"
    }
]);

// Add specific element animations for the animation extension.
