import "@nucleus/polyfill/src/_polyfill";
import "picturefill";
import "picturefill/dist/plugins/mutation/pf.mutation.min";
import "intersection-observer";
import { Component, Factory, Subject } from "@nucleus/core/src/_core";

export default class NucleusMPicture extends Component {

    static get rootClassName() {
        return "nuc-m-picture";
    }

    /**
     * Creates an instance of NucleusMPicture.
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);

        this._modifiers = {
            root: {
                loaded: "nuc-m-picture--loaded"
            }
        };

        /**
         * @type {Subject}
         * @private
         */
        this._loadSubject = new Subject();

        this._source = rootElement.querySelectorAll(".nuc-m-picture__source");
        this._image = rootElement.querySelector(".nuc-m-picture__image");

        this._image.addEventListener("load", (event) => this._loadSubject.notify(this, event));

        this._init();
    }

    /**
     * Subject for load event.
     * @return {Subject}
     */
    get loadSubject() {
        return this._loadSubject;
    }

    /**
     * Initializes all event listeners and the initial state.
     */
    _init() {
        if ("IntersectionObserver" in window) {
            let lazyImageObserver = new IntersectionObserver((entries, observer) => {
                entries.forEach(entry => {
                    if (entry.isIntersecting) {
                        this.loadPictures();
                        lazyImageObserver.unobserve(entry.target);
                    }
                });
            }, {
                rootMargin: "250px"
            });

            lazyImageObserver.observe(this.rootElement);
        } else {
            this.loadPictures();
        }
    }

    /**
     * Load the pictures by setting the srcset.
     */
    loadPictures() {
        if (this.hasClass(this._modifiers.root.loaded)) {
            return;
        }
        this._source.forEach(source => {
            source.setAttribute("srcset", source.getAttribute("data-srcset"));
        });
        this.addClass(this._modifiers.root.loaded);
    }
}

Factory.registerComponent(NucleusMPicture);
