import "@nucleus/polyfill/src/_polyfill";
import { Component, Factory } from "@nucleus/core/src/_core";
import NucleusOFooter from "@nucleus/o-footer";
import "intersection-observer";


export default class SchwarzOStickyBar extends Component {
    /**
     * @inheritDoc
     */
    static get rootClassName() {
        return "scwz-o-sticky-bar";
    }

    /**
     * Creates an instance of SchwarzOStickyBar.
     *
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);
        /**
         * Function to get to the top of the page
         * @type {NucleusAButton}
         * @private
         */
        this.toTopButton = Factory.createComponent(this.querySelector(".scwz-o-sticky-bar__button"));
        this.toTopButton.clickSubject.subscribe(function () {
            window.scrollTo(0, 0);
        });
    }
}

Factory.registerComponent(SchwarzOStickyBar);
