import "@nucleus/polyfill/src/_polyfill";
import { Component, Factory } from "@nucleus/core/src/_core";
import EventUtils from "@nucleus/core/src/_eventUtils";

export default class SchwarzGroupAKeyvisualAnimation extends Component {

    /**
     * @inheritDoc
     */
    static get rootClassName() {
        return "scgr-a-keyvisual-animation";
    }

    /**
     * Creates an instance of SchwarzGroupAKeyvisualAnimation.
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);

        this._parentContainer = this.rootElement.parentNode;
        this._parentContainerPosY = this._currentParentPos;
        this._parentContainerHeight = this._currentParentHeight;

        this._parentContainer.style.position = "relative";

        this._initializedAnimation = false;
        this._initializedStatic = false;

        window.addEventListener("scroll", this._initKeyVisual);
        window.addEventListener("resize", () => this.resetKeyVisual());
        this._updateKeyVisual();
    }

    /**
     * Initialize keyvisual.
     * @private
     */
    _initKeyVisual = EventUtils.throttle(() => {
        this._updateKeyVisual();
    });

    /**
     * Update keyvisual if needed.
     * @private
     */
    _updateKeyVisual = () => {
        if (!this._initializedAnimation) {
            if (this._containerHasEnoughSpace()) {
                this._currentPosY = this._currentPos;
                this._direction = "toRight";
                this._maxPos = 0;
                this._minPos = this._parentContainerPosY + this._parentContainerHeight + window.innerHeight;

                this.rootElement.style.maxHeight = window.innerHeight + "px";
                window.removeEventListener("scroll", this._initKeyVisual);
                this._initializedAnimation = true;

                window.addEventListener("scroll", this._checkVisibility);
                this._checkVisibility();
            } else if (!this._initializedStatic) {
                this.rootElement.style.maxHeight = window.innerHeight / 2 + "px";
                this.addClass("scgr-a-keyvisual-animation--static");

                this._initializedStatic = true;
            }
        }
    }

    /**
     * Check if before and after container is enough space to animate.
     * @private
     */
    _containerHasEnoughSpace() {
        this._parentContainerPosY = this._currentParentPos;
        this._parentContainerHeight = this._currentParentHeight;
        let height = Math.max(
            document.body.scrollHeight,
            document.body.offsetHeight,
            document.documentElement.clientHeight,
            document.documentElement.scrollHeight,
            document.documentElement.offsetHeight
        );

        if ((this._parentContainerPosY - window.innerHeight >= 0) && (this._parentContainerPosY + this._parentContainerHeight + window.innerHeight <= height)) {
            return true;
        } else {
            return false;
        }
    }

    /**
     * Check if container is visible.
     * @private
     */
    _checkVisibility = () => {
        if (this._containerHasEnoughSpace()) {
            this._parentContainerPosY = this._currentParentPos;
            this._parentContainerHeight = this._currentParentHeight;
            this._currentPosY = this._currentPos;
            let visibilePart = (100 / (this._parentContainerPosY + this._parentContainerHeight + window.innerHeight - this._parentContainerPosY)) * (this._currentPosY - this._parentContainerPosY); // in percentage

            if (this._currentPosY >= this._parentContainerPosY && (this._currentPosY <= (this._parentContainerPosY + this._parentContainerHeight + window.innerHeight))) {
                this._calculatePosition(visibilePart);
            } else {
                // reset position
                if (this._currentPosY < this._parentContainerPosY) {
                    this._direction = "toRight";
                    this.rootElement.style.left = 0;
                    this._maxPos = 0;
                } else {
                    this._direction = "toLeft";
                    this.rootElement.style.left = 0;
                    this._minPos = this._parentContainerPosY + this._parentContainerHeight + window.innerHeight;
                }
            }
        } else {
            this.resetKeyVisual();
        }

    }

    /**
     * Calculate position of keyvisual.
     * @param percentage
     * @private
     */
    _calculatePosition(percentage) {
        if (this._direction === "toRight") {
            this._minPos = this._parentContainerPosY + this._parentContainerHeight + window.innerHeight;
            if (this._currentPosY >= this._maxPos) {
                this._maxPos = this._currentPosY;
                this.rootElement.style.left = (((window.innerWidth + this.rootElement.offsetWidth) / 100) * percentage) + "px";
            }
        } else {
            this._maxPos = 0;
            if (this._currentPosY <= this._minPos) {
                this._minPos = this._currentPosY;
                this.rootElement.style.left = (window.innerWidth + this.rootElement.offsetWidth) - (((window.innerWidth + this.rootElement.offsetWidth) / 100) * percentage) + "px";
            }
        }
    }

    /**
     * Return current y position.
     * @returns {number}
     * @private
     */
    get _currentPos() {
        return window.innerHeight + window.pageYOffset;
    }

    /**
     * Return current y position of parent element.
     * @returns {number}
     * @private
     */
    get _currentParentPos() {
        return this.getAbsolutePositionTop(this._parentContainer) + window.pageYOffset;
    }

    /**
     * Return current height of parent element.
     * @returns {number}
     * @private
     */
    get _currentParentHeight() {
        return this._parentContainer.offsetHeight;
    }

    /**
     * Reset keyvisual.
     * @public
     */
    resetKeyVisual() {
        if (this._initializedAnimation) {
            this._initializedAnimation = false;
            this.rootElement.style.removeProperty("left");
            window.removeEventListener("scroll", this._checkVisibility);
            window.addEventListener("scroll", this._initKeyVisual);
        }

        if (this._initializedStatic) {
            this._initializedStatic = false;
            this.removeClass("scgr-a-keyvisual-animation--static");
        }

        this._updateKeyVisual();
    }

}

Factory.registerComponent(SchwarzGroupAKeyvisualAnimation);