import * as mergeOptions from "merge-options";
import { BREAKPOINTS } from "./_constants";

class _Factory {

    constructor() {
        this._components = {};
        this._moduleCores = [];
    }

    /**
     * Register a component class in the factory.
     * @param componentClass
     */
    registerComponent(componentClass) {
        this._components[componentClass.rootClassName] = componentClass;
    }

    /**
     * Register a module core instance in the factory.
     * @param moduleCoreInstance
     */
    registerModuleCore(moduleCoreInstance) {
        this._moduleCores.push(moduleCoreInstance);
    }

    /**
     * Creates a nucleus object instance of this element.
     * @param {Element} rootElement
     * @return {*}
     */
    createComponent(rootElement) {
        if (rootElement) {
            if (rootElement._nucleusInstance) {
                return rootElement._nucleusInstance;
            }

            let classDefinition = null;
            Array.from(rootElement.classList).forEach(className => {
                if (!classDefinition && this._components[className]) {
                    classDefinition = this._components[className];
                }
            });

            if (!classDefinition) {
                classDefinition = Component;
            }

            try {
                let instance = new classDefinition(rootElement);
                rootElement._nucleusInstance = instance;
                return instance;
            } catch (exception) {
                console.error("[nucleus] Error during initialization", rootElement, exception);
            }
        }

        return null;
    }

    /**
     *
     * @param {Element[]|NodeList|HTMLCollection} rootElements
     * @return {*[]}
     */
    createComponents(rootElements) {
        return Array.from(rootElements, rootElement => this.createComponent(rootElement));
    }

    /**
     * Inits all component instances on the page.
     * @param {Element} scope
     */
    initAll(scope) {
        scope = scope || document.body;
        Object.keys(this._components).forEach(className => {
            this.createComponents(scope.querySelectorAll("." + className));
        });

        this.createComponent(scope);

        this._moduleCores.forEach((moduleCore) => {
            moduleCore.initAll(scope);
        });
    }
}

export const Factory = new _Factory();

export class Component {

    /**
     *
     * @param {Element} rootElement
     * @param {boolean} [callInitialize]
     */
    constructor(rootElement, callInitialize) {
        /**
         * @deprecated
         * @protected
         */
        this._selectors = {};

        /**
         * @type {{Element}}
         * @deprecated
         * @protected
         */
        this._elements = {
            root: rootElement
        };

        /**
         * @private
         */
        this._modifiersObject = {
            root: {
                hidden: "hidden@all"
            }
        };

        /**
         * @private
         */
        this._attributesObject = {
        };

        /**
         * @type {Element}
         * @private
         */
        this._rootElement = rootElement;

        if (callInitialize) {
            setTimeout(() => {
                this._initialize();
            }, 0);
        }

        /**
         * @type {Subject}
         * @private
         */
        this._visibilitySubject = new Subject();
    }

    /**
     * Additional initialization method which is called after all child constructors are called.
     * @protected
     */
    _initialize() {
        throw new Error("Dont call Component._initialize! You have to override the method _initialize!");
    }

    /**
     * @deprecated
     * @protected
     */
    _addSelectors(selectors) {
        Object.assign(this._selectors, selectors);
    }

    /**
     * @deprecated
     * @protected
     */
    _addElements(elements) {
        Object.assign(this._elements, elements);
    }

    /**
     * @deprecated
     * @protected
     */
    _addModifiers(modifiers) {
        this._modifiersObject = mergeOptions(this._modifiersObject, modifiers);
    }

    /**
     * @deprecated
     * @protected
     */
    _addAttributes(attributes) {
        this._attributesObject = mergeOptions(this._attributesObject, attributes);
    }

    /**
     * Returns the root element which is bound to this instance.
     * @return {Element}
     */
    get rootElement() {
        return this._rootElement;
    }

    /**
     * Returns the root class name of this component class.
     * @abstract
     * @return {string}
     */
    static get rootClassName() {
        throw new Error("You have to implement the static method rootClassName!");
    }

    static get rootSelector() {
        return "." + this.rootClassName;
    }

    /**
     * Returns the root class name of this component.
     * @return {string}
     */
    get rootClassName() {
        return this.constructor.rootClassName;
    }

    /**
     * Return the ID of the root element. If there is no explicit ID set on the element, a random ID will be generated.
     * @return {string}
     */
    get rootId() {
        if (this._rootElement.id === "") {
            let rootClassName;

            try {
                rootClassName = this.rootClassName;
            } catch (e) {
                let classes = Array.from(this.rootElement.classList);
                if (classes.length) {
                    rootClassName = classes[0];
                } else {
                    rootClassName = "unknown";
                }
            }

            this._rootElement.id = rootClassName + "-" + Math.random().toString(36).substr(2, 16);
        }

        return this._rootElement.id;
    }

    /**
     * Hides the component.
     */
    hide() {
        this._rootElement.classList.add(this._modifiers.root.hidden);
        this._visibilitySubject.notify(this, false);
    }

    /**
     * Shows hidden component.
     */
    show() {
        this._rootElement.classList.remove(this._modifiers.root.hidden);
        this._visibilitySubject.notify(this, true);
    }

    /**
     * Toggles component visibility.
     */
    toggle() {
        this._rootElement.classList.toggle(this._modifiers.root.hidden);
    }

    /**
     * Returns all modifiers for this Component.
     * @return {Object.<string, Object.<string, string>>}
     * @protected
     */
    get _modifiers() {
        return this._modifiersObject;
    }

    /**
     * Add the modifiers to the internal object. Modifiers are merge recursive.
     * @param modifiers {Object.<string, Object.<string, string>>}
     * @protected
     */
    set _modifiers(modifiers) {
        this._modifiersObject = mergeOptions(this._modifiers, modifiers);
    }

    /**
     * Returns all attributes for this Component.
     * @return {Object.<string, Object.<string, string>>}
     * @protected
     */
    get _attributes() {
        return this._attributesObject;
    }

    /**
     * Add the attributes to the internal object. attributes are merge recursive.
     * @param attributes {Object.<string, Object.<string, string>>}
     * @protected
     */
    set _attributes(attributes) {
        this._attributesObject = mergeOptions(this._attributesObject, attributes);
    }


    /**
     * Returns the first element that is a descendant of the element on which it is invoked that matches the specified
     * group of selectors.
     * @param selectors {string}
     * @return {Element}
     */
    querySelector(selectors) {
        return this._rootElement.querySelector(selectors);
    }

    /**
     * Returns a static (not live) NodeList representing a list of elements matching the specified group of selectors
     * which are descendants of the component on which the method was called.
     * @param selectors {string}
     * @return {NodeList}
     */
    querySelectorAll(selectors) {
        return this._rootElement.querySelectorAll(selectors);
    }

    /**
     * Sets a specified attribute to true or false. Works as a proxy for the "setAttribute"-method.
     * @param name {string}
     * @param value {string} // Automatically converted to a string, if a non-string is passed.
     */
    setAttribute(name, value) {
        this._rootElement.setAttribute(name, value);
    }

    /**
     * Returns the value of a specified attribute on the element. If value does not exist, will return null or an empty string.
     * @param attributeName {string}
     * @return {string}
     */
    getAttribute(attributeName) {
        return this._rootElement.getAttribute(attributeName);
    }

    /**
     * Returns a boolean value, if an element has an attribute
     * @param name {string}
     * @return {boolean}
     */
    hasAttribute(name) {
        return this._rootElement.hasAttribute(name);
    }

    /**
     * Adds the specified `className` to the class attribute.
     * @param className {string}
     */
    addClass(className) {
        this._rootElement.classList.add(className);
    }

    /**
     * Removes the specified `className` to the class attribute.
     * @param className {string}
     */
    removeClass(className) {
        this._rootElement.classList.remove(className);
    }

    /**
     * Removes `className` from the class attribute if it exists, or adds `className` to the class attribute if it doesn't.
     * @param className {string}
     * @return {boolean}
     */
    toggleClass(className) {
        return this._rootElement.classList.toggle(className);
    }

    /**
     * Returns `true` if the class attributes contains the given `className`, otherwise `false`.
     * @param className {string}
     * @return {boolean}
     */
    hasClass(className) {
        return this._rootElement.classList.contains(className);
    }

    /**
     * Sets focus on the specified element, if it can be focused. The focused element is the element which will receive
     * keyboard and similar events by default.
     */
    focus() {
        this._rootElement.focus();
    }

    /**
     * Returns the the y-position of an element relative to top of page or the given endElement.
     * @param element The element for which the position is calculated
     * @param endElement The element up to which the calculation is done
     * @return {number}
     */
    getAbsolutePositionTop(element, endElement) {
        let positionTop = 0;

        while (element && element !== endElement) {
            if (element.tagName === "BODY") {
                let yScroll = element.scrollTop || document.documentElement.scrollTop;
                positionTop += (element.offsetTop - yScroll + element.clientTop);
            } else {
                positionTop += (element.offsetTop - element.scrollTop + element.clientTop);
            }

            element = element.offsetParent;
        }

        return positionTop;
    }

    /**
     * Subject for visibility event.
     * @return {Subject}
     */
    get visibilitySubject() {
        return this._visibilitySubject;
    }
}

/**
 * Helper class to implement a observer pattern.
 */
export class Subject {

    constructor() {
        this.handlers = [];
    }

    /**
     * Adds a observer to the subject
     *
     * @param {function} callback
     */
    subscribe(callback) {
        this.handlers.push(callback);
    }

    /**
     * Removes a observer to the subject
     *
     * @param {function} callback
     */
    unsubscribe(callback) {
        this.handlers = this.handlers.filter(currentCallback => {
            if (currentCallback !== callback) {
                return item;
            }
        });
    }

    /**
     * Triggers a event and notifies all observers.
     *
     * @param object object to which the subject belong
     * @param {Event} [originalEvent] original event which caused the subject change
     * @param [thisObj] context in which the observer callback should be executed, if nothing is set window is used
     */
    notify(object, originalEvent, thisObj) {
        var scope = thisObj || window;
        this.handlers.forEach(currentCallback => {
            currentCallback.call(window, object, originalEvent);
        });
    }
}

export class AbstractModuleCore {
    /**
     * Returns the root selector for this module core.
     * @abstract
     * @return {string}
     */
    get rootSelector() {
        throw new Error("You have to implement the method rootSelector!");
    }

    /**
     * Initializes the module for the given element.
     * @param {Element} rootElement
     * @returns {void}
     */
    createModule(rootElement) {
        throw new Error("You have to implement the method createModule!");
    }

    initAll(scope) {
        scope = scope || document.body;

        scope.querySelectorAll(this.rootSelector).forEach(element => {
            this.createModule(element);
        });

        if (scope.matches(this.rootSelector)) {
            this.createModule(scope);
        }
    }
}

class _StyleHoistingCore extends AbstractModuleCore {
    constructor() {
        super();

        /**
         * @type {string}
         * @private
         */
        this._selector = Object.keys(BREAKPOINTS)
            .map(breakpoint => `[data-style-${breakpoint}]`)
            .join(", ");

        /**
         * @type {HTMLStyleElement}
         * @private
         */
        this._styleElement = document.createElement("style");
        this._styleElement.id = "nucleusInlineStyles";
        document
            .getElementsByTagName("head")[0]
            .appendChild(this._styleElement);

        /**
         * @type {CSSStyleSheet}
         * @private
         */
        this._stylesheet = this._styleElement.sheet;

        /**
         * @type {Object.<string, CSSStyleSheet|CSSMediaRule>}
         * @private
         */
        this._groupingRules = Object.fromEntries(
            Object.entries(BREAKPOINTS)
                .reverse()
                .map(([breakpoint, minWidth]) => {
                    if (minWidth === 0) {
                        return [breakpoint, this._stylesheet];
                    } else {
                        const atRule = `@media (min-width: ${minWidth}px){}`;
                        this._stylesheet.insertRule(atRule);
                        return [breakpoint, this._stylesheet.cssRules[0]];
                    }
                })
        );
    }

    /**
     * @inheritDoc
     */
    get rootSelector() {
        return this._selector;
    }

    /**
     * @inheritDoc
     */
    createModule(rootElement) {
        for (let breakpoint in BREAKPOINTS) {
            const dataAttribute =
                BREAKPOINTS[breakpoint] === 0
                    ? "style"
                    : `data-style-${breakpoint}`;

            if (rootElement.hasAttribute(dataAttribute)) {
                const styles = rootElement.getAttribute(dataAttribute);
                const cssRule = this._buildCSSRule(rootElement, styles);
                this._groupingRules[breakpoint].insertRule(cssRule, 0);
                rootElement.removeAttribute(dataAttribute);
            }
        }
    }

    /**
     * @param {Element} rootElement
     * @param {String} styles
     * @private
     */
    _buildCSSRule(rootElement, styles) {
        const component = Factory.createComponent(rootElement);
        return `#${component.rootId}{${styles}}`;
    }
}
const StyleHoistingCore = new _StyleHoistingCore();
Factory.registerModuleCore(StyleHoistingCore);
