import "@nucleus/polyfill/src/_polyfill";
import { Component, Factory } from "@nucleus/core/src/_core";

import iframeResize from "iframe-resizer/js/iframeResizer.js";

export default class NucleusAIframe extends Component {

    /**
     * @inheritDoc
     */
    static get rootClassName() {
        return "nuc-a-iframe";
    }

    /**
     * Creates an instance of NucleusAInput.
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);

        this._modifiers = {
            root: {
                autoresize: {
                    none: "nuc-a-iframe--autoresize-none"
                }
            }
        };

        if (!this.hasClass(this._modifiers.root.autoresize.none)) {
            this.rootId; // force creation of ID attribute
            this.resizer = iframeResize({}, this.rootElement);
        }
    }
}

Factory.registerComponent(NucleusAIframe);
