import { Component, Factory } from "@nucleus/core/src/_core";

export default class NucleusMAlert extends Component {

    static get rootClassName() {
        return "nuc-m-alert";
    }

    /**
     * Creates an instance of NucleusMAlert.
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);

        this._addSelectors({
            close: ".nuc-m-alert__close-button"
        });

        this._addElements({
            close: rootElement.querySelector(this._selectors.close)
        });

        if (this._elements.close) {
            let closeButton = Factory.createComponent(this._elements.close);
            closeButton.clickSubject.subscribe(() => {
                this.hide();
            });
        }
    }
}

Factory.registerComponent(NucleusMAlert);
