import "@nucleus/polyfill/src/_polyfill";
import BezierEasing from "bezier-easing";

/**
 * @typedef {Object} NucleusEAnimationItemSchema
 * @property {String} animation
 * @property {String} value
 */

export default class NucleusEAnimationItem {
    /**
     * Creates an instance of NucleusEAnimationItem.
     *
     * @param {Element} rootElement
     * @param {NucleusEAnimation} animationGroup
     * @param {NucleusEAnimationItemSchema} data
     */
    constructor(rootElement, animationGroup, data) {
        this._rootElement = rootElement;
        this._animationGroup = animationGroup;
        this._data = data;

        this._cssValueArray = this._cssValueData;
        this._delayTimeout = null;
        this._animationFinished = false;
        this._animationIsActive = false;
        let dataAnimationArray = this._data.animation.split(" ");
        this._cssProperty = dataAnimationArray[0];
        this._duration = this._parseDuration(dataAnimationArray[1]);
        this._timingFunction = this._parseTimingFunction(dataAnimationArray[2]);

        if (dataAnimationArray[3]) {
            this._delay = this._parseDelay(dataAnimationArray[3]);
        } else {
            this._delay = this._parseDelay(0);
        }

        this._cssStartValue = Number(this._cssValueArray[2]);
        this._cssFinalValue = this._cssValueBeforeAnimation;
        this._cssCurrentValue = this._cssStartValue;
    }

    /**
     * Returns css value.
     * @returns {RegExpExecArray}
     * @private
     */
    get _cssValueData() {
        let result = /([^0-9|.|-]*)(\-?\d*[.]?\d+)(.*)/.exec(this._data.startValue);

        return result;
    }

    /**
     * Returns css final value.
     * @returns {number}
     * @private
     */
    get _cssValueBeforeAnimation() {
        let value = Number(getComputedStyle(this._rootElement).getPropertyValue(this._cssProperty));

        if (!isNaN(value)) {
            return value;
        } else {
            return 0;
        }
    }

    /**
     * Creates current css value string.
     * @returns {string}
     * @private
     */
    get _createCurrentCssValueString() {
        let result = "";
        let i = 0;

        for (let part of this._cssValueArray) {
            if (i === 0) {
                i++;
                continue;
            }

            if (i === 2) {
                result += this._cssCurrentValue;
            } else {
                result += part;
            }

            i++;
        }

        return result;
    }

    /**
     * Returns css property.
     * @returns {string}
     */
    get cssProperty() {
        return this._cssProperty;
    }

    /**
     * Returns true if animation is finished.
     * @returns {boolean}
     */
    get animationFinished() {
        return this._animationFinished;
    }

    /**
     * Returns true if animation is active.
     * @returns {boolean}
     */
    get animationIsActive() {
        return this._animationIsActive;
    }

    /**
     * Returns timing function.
     * @param {String} timingFunction
     * @returns {bezier.EasingFunction}
     * @private
     */
    _parseTimingFunction(timingFunction) {
        let result = null;
        try {
            if (NucleusEAnimationItem._timingFunctions[timingFunction]) {
                result = BezierEasing(NucleusEAnimationItem._timingFunctions[timingFunction]);
            } else {
                result = BezierEasing(NucleusEAnimationItem._timingFunctions["default"]);
            }
        } catch (e) {
            result = BezierEasing(0, 0, 1, 1); // only if overwritten default value is invalid
        }

        return result;
    }

    /**
     * Returns duration value.
     * @param {String} duration
     * @returns {Number}
     * @private
     */
    _parseDuration(duration) {
        if (NucleusEAnimationItem._durations[duration]) {
            duration = NucleusEAnimationItem._durations[duration];
        }

        let value = this._parseCssTime(duration);

        if (value) {
            return value;
        } else {
            value = this._parseCssTime(NucleusEAnimationItem._durations["default"]);

            if (value) {
                return value;
            } else {
                return 2000; // only if overwritten default value is invalid
            }
        }
    }

    /**
     * Returns delay value.
     * @param {String} delay
     * @returns {Number}
     * @private
     */
    _parseDelay(delay) {
        if (NucleusEAnimationItem._delays[delay]) {
            delay = NucleusEAnimationItem._delays[delay];
        }

        let value = this._parseCssTime(delay);

        if (value) {
            return value;
        } else {
            value = this._parseCssTime(NucleusEAnimationItem._delays["default"]);

            if (value) {
                return value;
            } else {
                return 0; // only if overwritten default value is invalid
            }
        }
    }

    /**
     * @private
     * @param {String} time
     * @returns {Number|null}
     */
    _parseCssTime(time) {
        let result = /^(\+?\d*[.]?\d+)(ms|s)$/.exec(time);

        if (result) {
            let value = Number.parseFloat(result[1]);
            let unit = result[2];

            if (unit === "s") {
                value *= 1000;
            }

            return value;
        } else {
            return null;
        }
    }

    /**
     * Removes css property.
     * @private
     */
    _removeProperty() {
        this._rootElement.style[this._cssProperty] = null;

        if (this._rootElement.getAttribute("style") === "") {
            this._rootElement.removeAttribute("style");
        }
    }

    /**
     * Initializes animation.
     */
    init() {
        if (this._delay === 0) {
            this._animationIsActive = true;
            this._animationGroup.startAnimation();
        } else {
            this._delayTimeout = setTimeout(() => {
                this._delayTimeout = null;
                this._animationIsActive = true;
                this._animationGroup.startAnimation();
            }, this._delay);
        }
    }

    /**
     * Resets animation.
     */
    reset() {
        if (this._delayTimeout) {
            clearTimeout(this._delayTimeout);
            this._delayTimeout = null;
        }

        this._animationFinished = false;
    }

    /**
     * Calculates css value.
     * @param {Number} elapsedTime
     * @returns {string|null}
     */
    calculate(elapsedTime) {
        elapsedTime = elapsedTime - this._delay;
        if (elapsedTime <= this._duration) {
            this._cssCurrentValue = this._cssStartValue + ((this._cssFinalValue - this._cssStartValue) * this._timingFunction(elapsedTime / this._duration));
            let result = this._createCurrentCssValueString;

            return result;
        } else {
            this._animationFinished = true;
            this._animationIsActive = false;
            this._removeProperty();

            return null;
        }
    }

    /**
     * Add custom duration.
     * @param {String} name
     * @param {String} time
     */
    static addDuration(name, time) {
        NucleusEAnimationItem._durations[name] = time;
    }

    /**
     * Add custom delay.
     * @param {String} name
     * @param {String} time
     */
    static addDelay(name, time) {
        NucleusEAnimationItem._delays[name] = time;
    }

    /**
     * Add custom timing function.
     * @param {String} name
     * @param {Array} values
     */
    static addTimingFunction(name, values) {
        NucleusEAnimationItem._timingFunctions[name] = values;
    }

    /**
     * @type {Object.<String, Array>}
     * @private
     */
    static _timingFunctions = {
        "default": [0, 0, 1, 1],
        "linear": [0, 0, 1, 1],
        "ease": [0.25, 0.1, 0.25, 1],
        "ease-in": [0.42, 0, 1, 1],
        "ease-out": [0, 0, 0.58, 1],
        "ease-in-out": [0.42, 0, 0.58, 1]
    };

    /**
     * @type {Object.<String, String>}
     * @private
     */
    static _delays = {
        "default": "0"
    };

    /**
     * @type {Object.<String, String>}
     * @private
     */
    static _durations = {
        "default": "2s"
    };
}