import {Component, Factory, Subject} from '@nucleus/core/src/_core';
import NucleusMTabItem from "@nucleus/m-tab-item";

export default class NucleusATabContent extends Component {

    static get rootClassName() {
        return "nuc-a-tab-content";
    }

    /**
     * Creates an instance of NucleusATabContent.
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);

        this._addSelectors({
            root: ".nuc-a-tab-content"
        });

        this._addAttributes({
            root: {
                ariaLabelledby: "aria-labelledby"
            }
        });

        /**
         * @type {NucleusMTabItem}
         * @private
         */
        this._linkedTabItem = null;
    }

    linkToTab(tabItem) {
        this._linkedTabItem = tabItem;
        this._linkedTabItem.stateSubject.subscribe(() => {
            this._linkedTabItem.state === NucleusMTabItem.States.SELECTED ? this.show() : this.hide();
        });
        this._elements.root.setAttribute(this._attributes.root.ariaLabelledby, tabItem.rootId);
    }
}

Factory.registerComponent(NucleusATabContent);