import "@nucleus/polyfill/src/_polyfill";
import {Component, Factory} from "@nucleus/core/src/_core";
import NucleusMSvgMapMarker from "@nucleus/m-svg-map-marker";

export default class NucleusOSvgMap extends Component {

    /**
     * @inheritDoc
     */
    static get rootClassName() {
        return "nuc-o-svg-map";
    }

    /**
     * Creates an instance of NucleusOSvgMap.
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);

        this._modifiers = {
            root: {
                initialising: "nuc-o-svg-map--initialising"
            },
            item: {
                active: "nuc-o-svg-map__item--active",
                current: "nuc-o-svg-map__item--current"
            }
        };

        /**
         * @type {NucleusAObject}
         * @private
         */
        this._mapObject = Factory.createComponent(this.querySelector(".nuc-o-svg-map__map"));

        /**
         * @type {Component}
         * @private
         */
        this._markerContainer = Factory.createComponent(this.querySelector(".nuc-o-svg-map__markers"));

        /**
         * @type {NucleusMSvgMapMarker[]}
         * @private
         */
        this._markerElements = Factory.createComponents(this._markerContainer.querySelectorAll(NucleusMSvgMapMarker.rootSelector));

        /**
         * @type {SVGPathElement[]}
         * @private
         */
        this._mapItemElements = [];

        const observer = new IntersectionObserver((entries, observer) => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    this._initMap();
                }
            });
        }, {
            root: document.documentElement
        });
        observer.observe(this.rootElement);

        this._mapObject.loadSubject.subscribe(() => {
            this._initMap();
        });
    }

    /**
     * checks if the map is ready (loaded and visible) and initializes it.
     * @private
     */
    _initMap() {
        if (!this._mapObject.isLoaded || this.isHidden) {
            return;
        }

        this._prepareActiveMapItemElements();
        this._addHighlightingOnHover();
        this._addLinksToMapItems();
        this._addMarkersToMap();
        this.removeClass(this._modifiers.root.initialising);
    }

    /**
     * Check, if class exists on SVG path. Workaround for IE11.
     * @param {SVGPathElement} target
     * @param {string} className
     * @return {boolean}
     * @private
     */
    _hasClassOnPath(target, className) {
        return !!target.className.baseVal.match(new RegExp("(\\s|^)" + className + "(\\s|$)"));
    }

    /**
     * Add class from SVG path. Workaround for IE11.
     * @param {SVGPathElement} target
     * @param {string} className
     * @private
     */
    _addClassToPath(target, className) {
        if (!this._hasClassOnPath(target, className)) {
            target.className.baseVal += " " + className;
        }
    }

    /**
     * Remove class from SVG path. Workaround for IE11.
     * @param {SVGPathElement} target
     * @param {string} className
     * @private
     */
    _removeClassFromPath(target, className) {
        if (this._hasClassOnPath(target, className)) {
            let reg = new RegExp("(\\s|^)" + className + "(\\s|$)");
            target.className.baseVal = target.className.baseVal.replace(reg, " ");
        }
    }

    /**
     * Get and activate path elements in svg, based on given markers.
     * @private
     */
    _prepareActiveMapItemElements() {
        this._markerElements.forEach(marker => {
            let mapItemId = marker.mapItemReferenceId;
            let mapItem = this._mapObject.contentDocument.querySelector("#" + mapItemId);

            if (mapItem) {
                this._addClassToPath(mapItem, this._modifiers.item.active);
                this._mapItemElements.push(mapItem);
                marker.show();
            } else {
                console.warn("[nucleus] Identifier '" + mapItemId + "' set by a map marker is missing in SVG file. Please add a corresponding ID to SVG path element.");
                marker.hide();
            }
        });
    }

    /**
     * Get specific path element from svg map by given reference ID.
     * @param {string} mapItemId
     * @return {SVGPathElement}
     * @private
     */
    _getMapItemById(mapItemId) {
        let mapItemElement = null;
        this._mapItemElements.forEach(item => {
            if (item.id === mapItemId) {
                mapItemElement = item;
            }
        });
        return mapItemElement;
    }

    /**
     * Get specific marker by given reference ID.
     * @param {string} mapItemId
     * @return {NucleusMSvgMapMarker}
     * @private
     */
    _getMarkerById(mapItemId) {
        let markerElement = null;
        this._markerElements.forEach(marker => {
            if (marker.mapItemReferenceId === mapItemId) {
                markerElement = marker;
            }
        });
        return markerElement;
    }

    /**
     * Get a specific map item's midpoint.
     * @param {string} mapItemId
     * @return {{left: string, top: string}}
     * @private
     */
    _calculateMidpoint(mapItemId) {
        let mapBoundingBox = this._mapObject.rootElement.getBoundingClientRect();
        let countryElement = this._getMapItemById(mapItemId);
        let anchorElement = this._mapObject.contentDocument.querySelector("#" + mapItemId + "-anchor");

        if (anchorElement) {
            let boundingBoxAnchor = anchorElement.getBoundingClientRect();

            return {
                left: (boundingBoxAnchor.left / mapBoundingBox.width) * 100 + "%",
                top: (boundingBoxAnchor.top / mapBoundingBox.height) * 100 + "%"
            };
        } else {
            let boundingBox = countryElement.getBoundingClientRect();

            return {
                left: ((boundingBox.left + boundingBox.width / 2) / mapBoundingBox.width) * 100 + "%",
                top: ((boundingBox.top + boundingBox.height / 2) / mapBoundingBox.height) * 100 + "%"
            };
        }
    }

    /**
     * Place markers onto map.
     * @private
     */
    _addMarkersToMap() {
        this._mapItemElements.forEach(mapItem => {
            let mapItemId = mapItem.id;
            let marker = this._getMarkerById(mapItemId);

            marker.position = this._calculateMidpoint(mapItemId);
        });
    }

    /**
     * Get hrefs from marker elements to use them for according map items.
     * @private
     */
    _addLinksToMapItems() {
        this._markerElements.forEach(marker => {
            let mapItemId = marker.mapItemReferenceId;
            let mapItemElement = this._getMapItemById(mapItemId);

            if (mapItemElement) {
                mapItemElement.addEventListener("click", () => {
                    this._getMarkerById(mapItemId).click();
                });
            }
        });
    }

    /**
     * Adds hover highlighting for markers and map items.
     * @private
     */
    _addHighlightingOnHover() {
        this._markerElements.forEach(marker => {
            let mapItemId = marker.mapItemReferenceId;
            let mapItemElement = this._getMapItemById(mapItemId);

            // marker highlighting
            marker.rootElement.addEventListener("mouseenter", () => {
                this._highlightingManager(mapItemId);
            });

            marker.rootElement.addEventListener("mouseleave", () => {
                this._highlightingManager(null);
            });

            // area highlighting
            if (mapItemElement) {
                mapItemElement.addEventListener("mouseenter", () => {
                    this._highlightingManager(mapItemId);
                });

                mapItemElement.addEventListener("mouseleave", () => {
                    this._highlightingManager(null);
                });
            }
        });
    }

    /**
     * Manage map item's and marker's hover state to ensure, both elements are highlighting simultaneously.
     * @param {string} mapItemId
     * @private
     */
    _highlightingManager(mapItemId) {
        this._mapItemElements.forEach(mapItem => {
            if (this._hasClassOnPath(mapItem, this._modifiers.item.current)) {
                this._removeClassFromPath(mapItem, this._modifiers.item.current);
            }
        });

        this._markerElements.forEach(marker => {
            marker.highlighted = false;
        });

        if (mapItemId !== null) {
            this._getMarkerById(mapItemId).highlighted = true;
            this._addClassToPath(this._getMapItemById(mapItemId), this._modifiers.item.current);
        }
    }

    /**
     * Returns true if the element is hidden, otherwise false.
     * @returns {boolean}
     */
    get isHidden() {
        return !this.isVisible;
    }

    /**
     * Returns true if the element is visible, otherwise false.
     * @returns {boolean}
     */
    get isVisible() {
        return !!( this.rootElement.offsetWidth
            || this.rootElement.offsetHeight
            || this.rootElement.getClientRects().length );
    }
}

Factory.registerComponent(NucleusOSvgMap);
