import "@nucleus/polyfill/src/_polyfill";
import {AbstractModuleCore, Factory} from "@nucleus/core/src/_core";
import { NunjucksWrapper } from "@nucleus/nunjucks-wrapper";
import {i18n} from "@nucleus/core/src/_i18n";
import NucleusAButton from "@nucleus/a-button";

class _MediaCore extends AbstractModuleCore {
    /**
     * @inheritDoc
     */
    get rootSelector() {
        return "[data-nucleus-e-media-viewer]";
    }

    /**
     * @inheritDoc
     */
    createModule(rootElement) {
        if (rootElement) {
            if (rootElement._nucleusMediaInstance) {
                return rootElement._nucleusMediaInstance;
            }

            if (rootElement.hasAttribute("data-nucleus-e-media-viewer")) {
                let instance = new NucleusEMediaViewer(rootElement);
                rootElement._nucleusMediaInstance = instance;
                return instance;
            }
        }

        return null;
    }
}

const MediaCore = new _MediaCore();
Factory.registerModuleCore(MediaCore);

export default class NucleusEMediaViewer {
    /**
     * Creates an instance of NucleusEMediaViewer.
     *
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        this._documentBody = document.querySelector("body");
        this._mediaViewerAttributes = JSON.parse(rootElement.dataset.nucleusEMediaViewer);
        this._objectMedia = this._mediaViewerAttributes.objectMedia;

        this._inlineContainer = document.querySelector(
            "#" + this._mediaViewerAttributes.containerId
        );
        this._mode = this._mediaViewerAttributes.mode;

        /**
         * @type {NucleusAButton}
         * @private
         */
        this._triggerButton = Factory.createComponent(rootElement);
        this._triggerButton.clickSubject.subscribe(() => {
            this._initMedia();
        });

        this._minimumElementWidth = 480; // Required minimum width of embedded Videos, recommended by Google
    }

    /**
     * Initialise the media element.
     * @private
     */
    _initMedia() {
        if (
            this._mode === "overlay" ||
            this._triggerButton.rootElement.offsetWidth <=
                this._minimumElementWidth
        ) {
            this._renderOverlay();
        } else {
            this._renderInline();
        }
    }

    /**
     * Build and render components as overlay.
     * @return {Promise<void>}
     * @private
     */
    async _renderOverlay() {
        let overlayData = {
            component: "nucleus/o-media-overlay",
            htmlAttributes: {
                "data-trigger-id": this._triggerButton.rootId
            },
            textTitleCloseButton: await i18n.translate("@nucleus/e-media-viewer.textTitleCloseButton"),
            embeddedMedia: [this._objectMedia]
        };

        this._documentBody.insertAdjacentHTML(
            "beforeend",
            await NunjucksWrapper.renderComponent(overlayData)
        );

        /**
         * @type {NucleusOMediaOverlay}
         * @private
         */
        let mediaOverlay = Factory.createComponent(document.querySelector("[data-trigger-id='" + this._triggerButton.rootId + "']"));

        Factory.initAll(mediaOverlay.rootElement);
    }

    /**
     * Build and render components inline.
     * @return {Promise<void>}
     * @private
     */
    async _renderInline() {
        this._inlineContainer.innerHTML = await NunjucksWrapper.renderComponent(
            this._objectMedia
        );

        Factory.initAll(this._inlineContainer);
    }
}