import "@nucleus/polyfill/src/_polyfill";
import { Factory, Subject } from "@nucleus/core/src/_core";
import NucleusMSvgMapMarker from "@nucleus/m-svg-map-marker";

export default class SchwarzMSvgMapMarker extends NucleusMSvgMapMarker {

    /**
     * @inheritDoc
     */
    static get rootClassName() {
        return "swrz-m-svg-map-marker";
    }

    /**
     * Creates an instance of NucleusMSvgMapMarker.
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);

        this._modifiers = {
            root: {
                current: "swrz-m-svg-map-marker--current"
            }
        };
    }

    /**
     * @inheritDoc
     */
    set highlighted(highlighted) {
        super.highlighted = highlighted;

        if (highlighted) {
            this.addClass(this._modifiers.root.current);
        } else {
            this.removeClass(this._modifiers.root.current);
        }
    }
}

Factory.registerComponent(SchwarzMSvgMapMarker);