import "@nucleus/polyfill/src/_polyfill";
import {Component, Factory} from "@nucleus/core/src/_core";
import { FOCUSABLE_ELEMENTS, KEYCODES } from "@nucleus/core/src/_constants";

export default class NucleusOMediaOverlay extends Component {

    /**
     * @inheritDoc
     */
    static get rootClassName() {
        return "nuc-o-media-overlay";
    }

    /**
     * Creates an instance of NucleusOMediaOverlay.
     *
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);

        this._documentBody = document.querySelector("body");
        this._overlayContent = rootElement.querySelector(".nuc-o-media-overlay__content");
        this._trigger = document.querySelector("#" + rootElement.dataset.triggerId);

        /**
         * @type {String}
         */
        this._documentBodyOverflowInital = "";

        /**
         * @type {Function}
         */
        this._boundKeyHandler = this._handleKeyEvents.bind(this);

        /**
         * @type {NucleusAButton}
         * @private
         */
        this._closeButton = Factory.createComponent(rootElement.querySelector(".nuc-o-media-overlay__close-button"));
        this._closeButton.clickSubject.subscribe(() => {
            this._close();
        });

        rootElement.addEventListener("click", event => {
            if (event.target === rootElement || event.target === this._overlayContent) {
                this._close();
            }
        });

        this._open();
    }

    /**
     * Open the overlay.
     * @private
     */
    _open() {
        const focusableNodes = this._getFocusableNodes();
        if (focusableNodes.length) {
            focusableNodes[0].focus();
        }
        this._documentBodyOverflowInital = this._documentBody.style.overflow; // remember this to restore later
        this._documentBody.style.overflow = "hidden";
        document.addEventListener("keydown", this._boundKeyHandler);
    }

    /**
     * Close the overlay.
     * @private
     */
    _close() {
        this._documentBody.style.overflow = this._documentBodyOverflowInital;
        if (this._trigger) {
            this._trigger.focus();
        }
        document.removeEventListener("keydown", this._boundKeyHandler);
        this.rootElement.parentNode.removeChild(this.rootElement);
    }

    /**
     * Handle key events while the overlay is open.
     * @param {Event} [event] original event
     * @private
     */
    _handleKeyEvents(event) {
        const key = event.code || event.keyCode;
        switch (key) {
            case KEYCODES.ESC:
            case "Escape":
            case "Esc":
                this._close();
                break;
            case KEYCODES.TAB:
            case "Tab":
                this._trapFocus(event);
                break;
            default:
                break;
        }
    }

    /**
     * Trap the focus inside the overlay so that user cannot move into the background page by tabbing.
     * @param {KeyboardEvent} [event] original event
     * @private
     */
    _trapFocus(event) {
        const focusableNodes = this._getFocusableNodes();
        if (!this.rootElement.contains(document.activeElement)) {
            focusableNodes[0].focus();
        } else {
            const focusedItemIndex = focusableNodes.indexOf(document.activeElement);
            if (event.shiftKey && focusedItemIndex === 0) {
                focusableNodes[focusableNodes.length - 1].focus();
                event.preventDefault();
            }
            if (!event.shiftKey && focusedItemIndex === focusableNodes.length - 1) {
                focusableNodes[0].focus();
                event.preventDefault();
            }
        }
    }

    /**
     * Find and return all focusable elements inside the overlay as array.
     * @return {Element[]}
     * @private
     */
    _getFocusableNodes() {
        const nodes = this.rootElement.querySelectorAll(FOCUSABLE_ELEMENTS);
        return Array.from(nodes);
    }
}

Factory.registerComponent(NucleusOMediaOverlay);
