import "@nucleus/polyfill/src/_polyfill";
import {Component, Factory, Subject} from '@nucleus/core/src/_core';

export default class NucleusAButton extends Component {

    /**
     * @inheritDoc
     */
    static get rootClassName() {
        return "nuc-a-button";
    }

    /**
     * Creates an instance of NucleusAButton.
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);

        /**
         * @type {Subject}
         * @private
         */
        this._clickSubject = new Subject();

        /**
         * @type {Subject}
         * @private
         */
        this._focusSubject = new Subject();

        /**
         * @type {Subject}
         * @private
         */
        this._blurSubject = new Subject();

        rootElement.addEventListener("click", (event) => this._clickSubject.notify(this, event));
        rootElement.addEventListener("focus", (event) => this._focusSubject.notify(this, event));
        rootElement.addEventListener("blur", (event) => this._blurSubject.notify(this, event));
    }

    /**
     * Subject for click event.
     * @return {Subject}
     */
    get clickSubject() {
        return this._clickSubject;
    }

    /**
     * Subject for focus event.
     * @return {Subject}
     */
    get focusSubject() {
        return this._focusSubject;
    }

    /**
     * Subject for blur event.
     * @return {Subject}
     */
    get blurSubject() {
        return this._blurSubject;
    }

    /**
     * Returns the disabled state of the button.
     * @return {boolean}
     */
    get disabled() {
        return this.rootElement.disabled;
    }

    /**
     * Sets the disabled state of the button.
     * @param {boolean} disabled
     */
    set disabled(disabled) {
        if (typeof disabled === "boolean") {
            this.rootElement.disabled = disabled;
        }
    }

    /**
     * Focus the button element.
     * @public
     */
    focus() {
        this.rootElement.focus();
    }
}

Factory.registerComponent(NucleusAButton);
