import "@nucleus/polyfill/src/_polyfill";
import { Component, Factory } from "@nucleus/core/src/_core";

export default class NucleusAMediaNative extends Component {

    /**
     * @inheritDoc
     */
    static get rootClassName() {
        return "nuc-a-media-native";
    }

    /**
     * Creates an instance of NucleusAMediaNative.
     *
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);

        this._playVideoOnIntersection();
    }

    /**
     * Play the video if container gets visible.
     * @private
     */
    _playVideoOnIntersection() {
        let observer = new IntersectionObserver((entries, observer) => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    let videoElement = entry.target.querySelector(".nuc-a-media-native__video");
                    let videoSourceElement = entry.target.querySelector(".nuc-a-media-native__source");

                    videoSourceElement.setAttribute("src", videoSourceElement.getAttribute("data-src"));
                    videoSourceElement.removeAttribute("data-src");
                    videoElement.load();

                    if (videoElement.hasAttribute("autoplay")) {
                        videoElement.muted = true;
                        videoElement.play();
                    }

                    observer.unobserve(entry.target);
                }
            });
        });
        observer.observe(this.rootElement);
    }
}

Factory.registerComponent(NucleusAMediaNative);
