
export default class NucleusLoader {
    /**
     *
     * @param {_NunjucksWrapper} nucleusWrapper
     */
    constructor(nunjucksWrapper) {
        this._nunjucksWrapper = nunjucksWrapper;
        this.async = false;
        this.loaded = {};
    }

    getSource(name, callback) {
        let path = name;
        path = "nunjucks/" + path.replace(".twig", ".njk.js");
        path = this._nunjucksWrapper.assetManifest[path];

        var result;
        this._fetch(path, function (error, src) {
            if (error) {
                if (callback) {
                    callback(error.content);
                } else if (error.status === 404) {
                    result = null;
                } else {
                    throw error.content;
                }
            } else {
                Function(src)();
                result = {
                    src: {
                        type: "code",
                        obj: window.nunjucksPrecompiled[name]
                    },
                    path: name
                };

                if (callback) {
                    callback(null, result);
                }
            }
        });

        return result;
    }

    _fetch(url, callback) {
        var xhrRequest = new XMLHttpRequest();
        var loading = true;

        xhrRequest.onreadystatechange = function () {
            if (xhrRequest.readyState === 4 && loading) {
                loading = false;

                if (xhrRequest.status === 0 || xhrRequest.status === 200) {
                    callback(null, xhrRequest.responseText);
                } else {
                    callback({
                        status: xhrRequest.status,
                        content: xhrRequest.responseText
                    });
                }
            }
        };

        xhrRequest.open("GET", url, this.async);
        xhrRequest.send();
    };
}
