import "@nucleus/polyfill/src/_polyfill";
import { Component, Factory } from "@nucleus/core/src/_core";
import HtmlUtils from "@nucleus/core/src/_htmlUtils";
import {BREAKPOINTS, KEYCODES} from "@nucleus/core/src/_constants";
import EventUtils from "@nucleus/core/src/_eventUtils";

export default class SchwarzOHeader extends Component {

    /**
     * @inheritDoc
     */
    static get rootClassName() {
        return "scwz-o-header";
    }

    /**
     * Creates an instance of SchwarzOHeader.
     * @param {Element} rootElement
     */
    constructor(rootElement) {
        super(rootElement);
        this._rootElement.removeAttribute("style");
        this._mobileBreakpoint = BREAKPOINTS.md;
        this._isMobile = window.innerWidth < this._mobileBreakpoint;
        this._isMD = window.innerWidth >= BREAKPOINTS.md && window.innerWidth < BREAKPOINTS.lg;
        this._initialWidth = window.innerWidth;

        this._collapsed = false;
        this._scrollTop = 0;

        this._resizeTime;
        this._resizeTimeout = false;

        this._mainNav = this._rootElement.querySelector(".scwz-o-header__main-navigation");
        this._subNav = this._rootElement.querySelector(".scwz-o-header__sub-navigation");
        this._primaryNav = this._rootElement.querySelector(".scwz-o-header__primary-navigation");
        this._activeMainNavItem = this._mainNav.querySelector(".scwz-m-header-main-nav-item--active .scwz-m-header-main-nav-item__anchor");
        this._activeMainNavItemList = this._mainNav.querySelector(".scwz-m-header-main-nav-item--active .scwz-m-header-nav-item-list");
        this._hamburgerNav = this._rootElement.querySelector(".scwz-o-header__hamburger-navigation");
        this._userNav = this._rootElement.querySelector(".scwz-o-header__user-navigation");

        if (this._isMobile) {
            this._rootElement.classList.add("scwz-o-header--mobile");
        }

        if (!this._subNav) {
            this._mainNav.classList.add("scwz-o-header__main-navigation--active");
        } else {
            this._subNavList = this._subNav.querySelector(".scwz-o-header__nav-list");
            this._subNavListItems = this._subNav.querySelectorAll(".scwz-m-header-nav-item");
        }

        if (this._isMobile && this._subNav) {
            this._subNavListItems.forEach((subNavListItem) => this._activeMainNavItemList.append(subNavListItem));
        }

        window.addEventListener("scroll", EventUtils.debounce((event) => this._handleWindowScroll(), 150));
        window.addEventListener("resize", (event) => this._handleWindowResize(event), false);
        window.addEventListener("click", (event) => this._handleWindowClick(event), false)

        if (this._activeMainNavItem) {
            this._activeMainNavItem.addEventListener("keydown", (event) => this._handleActiveMainNavItemKeydown(event), false);
        }

        this._setupMainAndSubNav();

        if (this._hamburgerNav) {
            this._hamburgerNav.addEventListener("click", () => this._handleHamburgerClick());

        }

        this._dispatchResize();
    }

    /**
     * closes all dropdowns when window is click outside of trigger element
     * @param  {Object} event info
     * @private
     */
    _handleWindowClick(event) {
        if (!event.target.matches(".scwz-o-header__nav-dropdown-trigger")) {
            this._hideDropdown();
        }
    }

    /**
     * handles collapsing and extending on window scroll
     * @param  {Object} event info
     * @private
     */
    _handleWindowScroll(event) {
        if (this._rootElement.classList.contains("scwz-o-header--mobile")) {
            return;
        }

        var st = window.pageYOffset || document.documentElement.scrollTop;

        if (st > this._scrollTop){
            this._collapse();
        } else {
            this._extend();
        }

        this._scrollTop = st <= 0 ? 0 : st;
    }

    /**
     * handles window resize
     * @param  {Object} event info
     * @private
     */
    _handleWindowResize(event) {
        var that = this;

        this._resizeTime = new Date();
        if (this._resizeTimeout === false) {
            this._resizeTimeout = true;
            setTimeout(function() {
                that._resizeend(event);
            }, 200);
        }

        var widthAfterResize = window.innerWidth;
        this._isMobile = widthAfterResize < this._mobileBreakpoint;

        if (this._initialWidth < widthAfterResize && this._initialWidth < this._mobileBreakpoint && !this._isMobile) { // mobile -> desktop
            this._rootElement.classList.remove("scwz-o-header--mobile");
            this._rootElement.classList.remove("scwz-o-header--mobile-extended");
            this._rootElement.classList.add("scwz-o-header--md");
            this._hamburgerNav.classList.remove("scwz-o-header__hamburger-navigation--active");
            this._primaryNav.setAttribute("aria-expanded", "false");
            document.querySelector("body").style.overflow = "auto";

            if (this._subNav) {
                let _activeMainNavItemListItem = this._activeMainNavItemList.querySelectorAll(".scwz-m-header-nav-item");
                _activeMainNavItemListItem.forEach((activeMainNavItemListItem) => this._subNavList.append(activeMainNavItemListItem));
            }

        } else if (this._initialWidth > widthAfterResize && this._initialWidth >= this._mobileBreakpoint && this._isMobile) { // desktop -> mobile
            this._rootElement.classList.add("scwz-o-header--mobile");
            this._rootElement.classList.remove("scwz-o-header--collapsed");
            this._rootElement.classList.add("scwz-o-header--extended");
            this._extend();

            if (this._subNav) {
                this._subNavListItems.forEach((subNavListItem) => this._activeMainNavItemList.append(subNavListItem));
            }
        }

        this._isMD = widthAfterResize < BREAKPOINTS.lg && !this._isMobile;

        if (this._isMobile) {
            this._rootElement.classList.remove("scwz-o-header--md");
            this._rootElement.classList.remove("scwz-o-header--lg");
        } else if (this._initialWidth < widthAfterResize && this._initialWidth < BREAKPOINTS.lg && !this._isMD) { // md -> lg
            this._rootElement.classList.remove("scwz-o-header--md");
            this._rootElement.classList.add("scwz-o-header--lg");
        } else if (this._initialWidth > widthAfterResize && this._initialWidth >= BREAKPOINTS.lg && this._isMD) { // lg -> md
            this._rootElement.classList.remove("scwz-o-header--lg");
            this._rootElement.classList.add("scwz-o-header--md");
        }

        this._setupMainAndSubNav(event);
        this._positionDropdown();

        this._initialWidth = widthAfterResize;
    }

    /**
     * handles navigation update and sub nav dropdown positioning on window resize end
     * @param  {Object} event info
     * @private
     */
    _resizeend(event) {
        var that = this;
        if (new Date() - this._resizeTime < 200) {
            setTimeout(function() {
                that._resizeend(event);
            }, 200);
        } else {
            this._resizeTimeout = false;
            this._setupMainAndSubNav(event);
            this._positionDropdown();
        }
    }

    /**
     * sets css classes on header collapse
     * @private
     */
    _collapse() {
        if (!this._collapsed) {
            let that = this;

            this._rootElement.classList.remove("scwz-o-header--extended");
            this._rootElement.classList.add("scwz-o-header--collapsed");
            this._rootElement.classList.remove("scwz-o-header--md");
            this._rootElement.classList.remove("scwz-o-header--lg");
            this._collapsed = true;

            setTimeout(function() {
                that._setupMainAndSubNav();
            }, 300)
        }
    }

    /**
     * sets css classes on header extend
     * @private
     */
    _extend() {
        if (this._collapsed) {
            let that = this;

            this._rootElement.classList.remove("scwz-o-header--collapsed");
            this._rootElement.classList.add("scwz-o-header--extended");
            this._rootElement.classList.remove("scwz-o-header--md");
            this._rootElement.classList.remove("scwz-o-header--lg");
            this._collapsed = false;

            setTimeout(function() {
                that._setupMainAndSubNav();
            }, 200)
        }
    }

    /**
     * handles arrow down key on active main nav item
     * @param  {Object} event info
     * @private
     */
    _handleActiveMainNavItemKeydown(event) {
        const key = event.code || event.keyCode;

        if ([KEYCODES.DOWN, "ArrowDown"].includes(key) && this._subNav) {
            event.preventDefault();
            this._subNav.querySelector(".scwz-m-header-sub-nav-item__anchor").focus();
        }
    }

    /**
     * updates sub nav dropdowns
     * @param  {Element} dropdown
     * @private
     */
    _updateDropdowns(list) {
        this._hideDropdown();
        list.classList.toggle("scwz-o-header__nav-dropdown-list--show");
        list.parentElement.querySelector(".scwz-o-header__nav-dropdown-trigger").setAttribute("aria-expanded", "true");
        this._positionDropdown(list);
    }

    /**
     * positions sub nav dropdowns
     * @param  {Element}
     * @private
     */
    _positionDropdown(list) {
        var dropdown = list || this._rootElement.querySelector(".scwz-o-header__nav-dropdown-list--show");
        if (dropdown) {
            dropdown.style.right = "auto";
            dropdown.style.left = "8px";

            var dropdownBounding = dropdown.getBoundingClientRect();

            if (dropdownBounding.right > (window.innerWidth || document.documentElement.clientWidth)) {
                dropdown.style.right = "8px";
                dropdown.style.left = "auto";
            }
        }
    }

    /**
     * hides all sub nav dropdowns
     * @private
     */
    _hideDropdown() {
        var dropdownList = this._rootElement.querySelector(".scwz-o-header__nav-dropdown-list--show");
        if (dropdownList) {
            dropdownList.style.right = "-10000px";
            dropdownList.style.left = "auto";
            dropdownList.classList.remove("scwz-o-header__nav-dropdown-list--show");
            dropdownList.parentElement.querySelector(".scwz-o-header__nav-dropdown-trigger").setAttribute("aria-expanded", "false");
        }
    }

    /**
     * handles which navs should be set up
     * @param  {Object} event info
     * @private
     */
    _setupMainAndSubNav(event) {
        if (this._mainNav) {
            this._handleNavItems(event, this._mainNav);
        }

        if (this._subNav) {
            this._handleNavItems(event, this._subNav);
        }
    }

    /**
     * sets up main and sub nav items for different header stages
     * @param  {Object} event info
     * @param  {Element} navigation
     * @private
     */
    _handleNavItems(event, nav) {
        var navItems = nav.querySelectorAll(".scwz-m-header-nav-item:not(.scwz-m-header-nav-sub-item)");
        var headerWidth = this._rootElement.querySelector(".scwz-o-header__navigation").offsetWidth;

        var moreDropdown = nav.querySelector(".scwz-o-header__nav-dropdown-container-more");
        var moreDropdownTrigger = moreDropdown.querySelector(".scwz-o-header__nav-dropdown-trigger");

        var list = nav.querySelector(".scwz-o-header__nav-list");
        var dropdownList = moreDropdown.querySelector(".scwz-o-header__nav-dropdown-list");

        var calculatedNavItems = 0;

        if (this._collapsed) {
            if (this._userNav) {
                headerWidth -= this._rootElement.querySelector(".scwz-o-header__user-navigation").offsetWidth  - 40;
            }

            if (event && event.type === "resize") {
                headerWidth -= 32;
            } else {
                this._hideDropdown();
            }
        } else {
            headerWidth += 16;
        }

        nav.style.width = headerWidth + "px";

        moreDropdownTrigger.classList.remove("scwz-o-header__nav-dropdown-trigger--active");

        Array.from(dropdownList.children).forEach((listItem) => list.append(listItem));

        if (!this._isMobile) {
            navItems.forEach((navItem, index, navItems) => {
                navItem.style.visibility = "visible";
                var navItemAnchor = navItem.querySelector(".scwz-m-header-nav-item__anchor");
                navItem.classList.remove("scwz-m-header-nav-item--sub");
                navItemAnchor.classList.remove("scwz-m-header-nav-item__anchor--sub");
                navItem.style.display = "inline-block";
                var navItemWidth = navItem.offsetWidth;
                moreDropdown.style.display = "block";
                var moreDropdownWidth = moreDropdownTrigger.offsetWidth;

                calculatedNavItems += navItemWidth;

                if (navItems.length - 1 === index) {
                    moreDropdownWidth = 0;
                    moreDropdown.style.display = "none";
                }

                if (calculatedNavItems + moreDropdownWidth > headerWidth) {
                    moreDropdown.style.display = "block";
                    moreDropdown.style.visibility = "visible";
                    if (navItem.classList.contains("scwz-m-header-sub-nav-item--active") || navItem.classList.contains("scwz-m-header-main-nav-item--active")) {
                        moreDropdownTrigger.classList.add("scwz-o-header__nav-dropdown-trigger--active");
                    }
                    navItem.classList.add("scwz-m-header-nav-item--sub");
                    navItemAnchor.classList.add("scwz-m-header-nav-item__anchor--sub");
                    navItemAnchor.setAttribute("tabindex", "-1");
                    dropdownList.append(navItem);
                }
            })

            var dropdowns = this._rootElement.querySelectorAll(".scwz-o-header__nav-dropdown-container");

            dropdowns.forEach((dropdown) => {
                this._setupDropdownEventHandler(dropdown);
                this._setupDropdownItems(dropdown);
            })
        } else {
            navItems.forEach((navItem, index, navItems) => {
                navItem.style.display = "block";
            })
        }

        this._handleTabindexes();
    }

    /**
     * handle taxindexes for different header states
     * @private
     */
    _handleTabindexes() {
        let logo = this._rootElement.querySelector(".scwz-o-header__logo-anchor");
        let triggersMainNav = this._rootElement.querySelectorAll(".scwz-o-header__main-navigation .scwz-o-header__nav-dropdown-trigger");
        let triggersMainNavSubItems = this._rootElement.querySelectorAll(".scwz-o-header__main-navigation .scwz-o-header__nav-dropdown-list .scwz-m-header-nav-item__anchor");
        let triggersSubNav = this._rootElement.querySelectorAll(".scwz-o-header__sub-navigation .scwz-o-header__nav-dropdown-trigger");
        let triggersSubNavSubItems = this._rootElement.querySelectorAll(".scwz-o-header__sub-navigation .scwz-o-header__nav-dropdown-list .scwz-m-header-nav-item__anchor");
        let mainNavItems = this._rootElement.querySelectorAll(".scwz-o-header__main-navigation .scwz-m-header-main-nav-item__anchor");
        let mainNavSubItems = this._rootElement.querySelectorAll(".scwz-o-header__main-navigation .scwz-m-header-sub-nav-item__anchor");
        let subNavItems = this._rootElement.querySelectorAll(".scwz-o-header__sub-navigation .scwz-o-header__nav-list .scwz-m-header-sub-nav-item__anchor");
        let brandNavItems = this._rootElement.querySelectorAll(".scwz-o-header__brand-navigation .scwz-m-header-brand-nav-item__anchor");
        let languageNavItems = this._rootElement.querySelectorAll(".scwz-o-header__language-navigation .scwz-m-header-language-nav-item__anchor");
        let userNavItems = this._rootElement.querySelectorAll(".scwz-o-header__user-navigation .scwz-m-header-user-nav-item__anchor");

        if (this._isMobile) {
            logo.removeAttribute("tabindex");
            brandNavItems.forEach((brandNavItem) => brandNavItem.removeAttribute("tabindex"));
            languageNavItems.forEach((languageNavItem) => languageNavItem.removeAttribute("tabindex"));
            userNavItems.forEach((userNavItem) => userNavItem.removeAttribute("tabindex"));
            mainNavItems.forEach((mainNavItem) => mainNavItem.removeAttribute("tabindex"));
            mainNavSubItems.forEach((mainNavSubItem) => mainNavSubItem.removeAttribute("tabindex"));
            triggersMainNav.forEach((triggerMainNav) => triggerMainNav.removeAttribute("tabindex"));
            triggersMainNavSubItems.forEach((triggersMainNavSubItem) => triggersMainNavSubItem.removeAttribute("tabindex"));
            subNavItems.forEach((subNavItem) => subNavItem.removeAttribute("tabindex"));
            triggersSubNav.forEach((triggerSubNav) => triggerSubNav.removeAttribute("tabindex"));
            triggersSubNavSubItems.forEach((triggersSubNavSubItem) => triggersSubNavSubItem.removeAttribute("tabindex"));
        } else if (!this._subNav && this._collapsed) {
            logo.setAttribute("tabindex", "1");
            brandNavItems.forEach((brandNavItem) => brandNavItem.setAttribute("tabindex", "-1"));
            languageNavItems.forEach((languageNavItem) => languageNavItem.setAttribute("tabindex", "-1"));
            userNavItems.forEach((userNavItem) => userNavItem.setAttribute("tabindex", "3"));
            mainNavItems.forEach((mainNavItem) => mainNavItem.setAttribute("tabindex", "2"));
            mainNavSubItems.forEach((mainNavSubItem) => mainNavSubItem.setAttribute("tabindex", "-1"));
            triggersMainNav.forEach((triggerMainNav) => triggerMainNav.setAttribute("tabindex", "2"));
            triggersMainNavSubItems.forEach((triggersMainNavSubItem) => triggersMainNavSubItem.setAttribute("tabindex", "-1"));
        } else if (!this._subNav && !this._collapsed) {
            logo.setAttribute("tabindex", "0");
            brandNavItems.forEach((brandNavItem) => brandNavItem.removeAttribute("tabindex"));
            languageNavItems.forEach((languageNavItem) => languageNavItem.setAttribute("tabindex", "-1"));
            userNavItems.forEach((userNavItem) => userNavItem.removeAttribute("tabindex"));
            mainNavItems.forEach((mainNavItem) => mainNavItem.removeAttribute("tabindex"));
            mainNavSubItems.forEach((mainNavSubItem) => mainNavSubItem.setAttribute("tabindex", "-1"));
            triggersMainNav.forEach((triggerMainNav) => triggerMainNav.removeAttribute("tabindex"));
            triggersMainNavSubItems.forEach((triggersMainNavSubItem) => triggersMainNavSubItem.setAttribute("tabindex", "-1"));
        } else if (this._subNav && this._collapsed) {
            logo.setAttribute("tabindex", "1");
            brandNavItems.forEach((brandNavItem) => brandNavItem.setAttribute("tabindex", "-1"));
            languageNavItems.forEach((languageNavItem) => languageNavItem.setAttribute("tabindex", "-1"));
            userNavItems.forEach((userNavItem) => userNavItem.setAttribute("tabindex", "3"));
            mainNavItems.forEach((mainNavItem) => mainNavItem.setAttribute("tabindex", "-1"));
            mainNavSubItems.forEach((mainNavSubItem) => mainNavSubItem.setAttribute("tabindex", "-1"));
            triggersMainNav.forEach((triggerMainNav) => triggerMainNav.setAttribute("tabindex", "-1"));
            subNavItems.forEach((subNavItem) => subNavItem.setAttribute("tabindex", "2"));
            triggersSubNav.forEach((triggerSubNav) => triggerSubNav.setAttribute("tabindex", "2"));
        } else if (this._subNav && !this._collapsed) {
            logo.removeAttribute("tabindex");
            brandNavItems.forEach((brandNavItem) => brandNavItem.removeAttribute("tabindex"));
            languageNavItems.forEach((languageNavItem) => languageNavItem.setAttribute("tabindex", "-1"));
            userNavItems.forEach((userNavItem) => userNavItem.removeAttribute("tabindex"));
            mainNavItems.forEach((mainNavItem) => mainNavItem.removeAttribute("tabindex"));
            mainNavSubItems.forEach((mainNavSubItem) => mainNavSubItem.setAttribute("tabindex", "-1"));
            triggersMainNav.forEach((triggerMainNav) => triggerMainNav.removeAttribute("tabindex"));
            triggersMainNavSubItems.forEach((triggersMainNavSubItem) => triggersMainNavSubItem.setAttribute("tabindex", "-1"));
            subNavItems.forEach((subNavItem) => subNavItem.removeAttribute("tabindex"));
            triggersSubNav.forEach((triggerSubNav) => triggerSubNav.removeAttribute("tabindex"));
            triggersSubNavSubItems.forEach((triggersSubNavSubItem) => triggersSubNavSubItem.setAttribute("tabindex", "-1"));
        }
    }

    /**
     * setup event handler for dropdown
     * @param  {Element} dropdown
     * @private
     */
    _setupDropdownEventHandler(dropdown) {
        var dropdownTrigger = dropdown.querySelector(".scwz-o-header__nav-dropdown-trigger");

        dropdownTrigger.addEventListener("focus", (event) => this._handleDropdownTriggerFocus(event), false);
        dropdownTrigger.addEventListener("mouseover", (event) => this._handleDropdownTriggerMouseover(event), false);
        dropdownTrigger.addEventListener("keydown", (event) => this._handleDropdownTriggerKeydown(event), false);
        dropdown.addEventListener("mouseleave", (event) => this._handleDropdownMouseleave(event), false);
    }

    /**
     * handles focus on dropdown trigger element
     * @param  {Object} event info
     * @private
     */
    _handleDropdownTriggerFocus(event) {
        if (!event.target.classList.contains("scwz-o-header__nav-dropdown-trigger")) {
            return;
        }

        if (event.target.nextElementSibling.classList.contains("scwz-o-header__nav-dropdown-list") && !event.target.parentElement.parentElement.classList.contains("scwz-o-header__nav-dropdown-list")) {
            this._updateDropdowns(event.target.nextElementSibling);
        }
    }

    /**
     * handles mouseout on dropdown trigger element
     * @param  {Object} event info
     * @private
     */
    _handleDropdownTriggerMouseover(event) {
        if (!event.target.classList.contains("scwz-o-header__nav-dropdown-trigger") || event.target.classList.contains("scwz-m-header-nav-item__anchor--sub")) {
            return;
        }

        var dropdownList = event.target.nextElementSibling;

        if (dropdownList) {
            this._updateDropdowns(dropdownList);
        }
    }

    /**
     * handles mouseleave on dropdown element
     * @param  {Object} event info
     * @private
     */
    _handleDropdownMouseleave(event) {
        if (event.target.classList.contains("scwz-o-header__nav-dropdown-container") && !event.target.classList.contains("scwz-m-header-nav-item--sub")) {
            this._hideDropdown();
        }
    }

    /**
     * handles keydown on dropdown trigger element
     * @param  {Object} event info
     * @private
     */
    _handleDropdownTriggerKeydown(event) {
        if (!event.target.classList.contains("scwz-o-header__nav-dropdown-trigger")) {
            return;
        }
        var dropdownContainer = HtmlUtils.ancestorHasClass(event.target, "scwz-o-header__nav-dropdown-container");

        if (dropdownContainer) {
            var dropdownList = dropdownContainer.querySelector(".scwz-o-header__nav-dropdown-list");
            const key = event.code || event.keyCode;

            if (event.shiftKey && ([KEYCODES.TAB, "Tab"].includes(key)) || [KEYCODES.TAB, "Tab"].includes(key)) {
                this._hideDropdown();
            } else if ([KEYCODES.DOWN, "ArrowDown"].includes(key)) {
                event.preventDefault();

                if (dropdownList.classList.contains("scwz-o-header__nav-dropdown-list--show") && HtmlUtils.ancestorHasClass(dropdownContainer, "scwz-o-header__main-navigation") || HtmlUtils.ancestorHasClass(dropdownContainer, "scwz-o-header__sub-navigation") || HtmlUtils.ancestorHasClass(dropdownContainer, "scwz-o-header__language-navigation")) {
                    dropdownList.querySelector(".scwz-m-header-nav-item:not(.scwz-m-header-nav-sub-item) .scwz-m-header-nav-item__anchor, .scwz-m-header-language-nav-item .scwz-m-header-language-nav-item__anchor").focus();
                }
            }  else if ([KEYCODES.UP, "ArrowUp"].includes(key)) {
                event.preventDefault();
            }
        }
    }

    /**
     * handles keydown on dropdown item element
     * @param  {Object} event info
     * @private
     */
    _handleDropdownListItemKeydown(event, itemTyp) {
        if ((itemTyp === "nav-item" && !event.target.classList.contains("scwz-m-header-nav-item__anchor")) || (itemTyp === "language-item" && !event.target.classList.contains("scwz-m-header-language-nav-item__anchor"))) {
            return;
        }

        var dropdownContainer = HtmlUtils.ancestorHasClass(event.target, "scwz-o-header__nav-dropdown-container");
        if (dropdownContainer) {
            const key = event.code || event.keyCode;

            var dropdownTrigger = dropdownContainer.querySelector(".scwz-o-header__nav-dropdown-trigger");
            var previousNavItem = event.target.parentElement.previousElementSibling;
            var nextNavItem = event.target.parentElement.nextElementSibling;

            if ([KEYCODES.UP, "ArrowUp"].includes(key) && previousNavItem && ((itemTyp === "nav-item" && previousNavItem.classList.contains("scwz-m-header-nav-item")) || (itemTyp === "language-item" && previousNavItem.classList.contains("scwz-m-header-language-nav-item")))) {
                event.preventDefault();
                previousNavItem.querySelector(".scwz-m-header-nav-item__anchor, .scwz-m-header-language-nav-item__anchor").focus();
            } else if ([KEYCODES.UP, "ArrowUp"].includes(key) && !previousNavItem) {
                event.preventDefault();
                dropdownTrigger.focus();
            } else if ([KEYCODES.DOWN, "ArrowDown"].includes(key) && nextNavItem && ((itemTyp === "nav-item" && nextNavItem.classList.contains("scwz-m-header-nav-item")) || (itemTyp === "language-item" && nextNavItem.classList.contains("scwz-m-header-language-nav-item")))) {
                event.preventDefault();
                nextNavItem.querySelector(".scwz-m-header-nav-item__anchor, .scwz-m-header-language-nav-item__anchor").focus();
            } else if ([KEYCODES.DOWN, "ArrowDown"].includes(key) && !nextNavItem) {
                event.preventDefault();
            } else if (event.shiftKey && ([KEYCODES.TAB, "Tab"].includes(key)) || [KEYCODES.TAB, "Tab"].includes(key)) { // hide dropdown if dropdown item is focused and then tab clicked.
                this._hideDropdown();
            }
        }
    }

    /**
     * setup items in dropdown list
     * @param  {Element} dropdown
     * @private
     */
    _setupDropdownItems(dropdown) {
        var list = dropdown.querySelector(".scwz-o-header__nav-dropdown-list");

        var items = list.querySelectorAll(".scwz-m-header-nav-item");
        var langItems = list.querySelectorAll(".scwz-m-header-language-nav-item");

        items.forEach(item => item.addEventListener("keydown", (event) => this._handleDropdownListItemKeydown(event, "nav-item"), false));
        langItems.forEach(item => item.addEventListener("keydown", (event) => this._handleDropdownListItemKeydown(event, "language-item"), false));
    }

    /**
     * handles click on hamburger button
     * @private
     */
    _handleHamburgerClick() {
        this._hamburgerNav.classList.toggle("scwz-o-header__hamburger-navigation--active");
        this._rootElement.classList.toggle("scwz-o-header--mobile-extended");

        if (this._hamburgerNav.classList.contains("scwz-o-header__hamburger-navigation--active")) {
            document.querySelector("body").style.overflow = "hidden";
            this._mainNav.scrollTo(0, 0);
            this._primaryNav.setAttribute("aria-expanded", "true");
        } else {
            document.querySelector("body").style.overflow = "auto";
            this._primaryNav.setAttribute("aria-expanded", "false");
        }
    }

    /**
     * Dispatches resize-event for modern browsers and IE
     * @private
     */
    _dispatchResize() {
        if (typeof(Event) === 'function') {
            window.dispatchEvent(new Event('resize'));
        } else {
            var e = window.document.createEvent('UIEvents');
            e.initUIEvent('resize', true, false, window, 0);
            window.dispatchEvent(e);
        }
    }

}

Factory.registerComponent(SchwarzOHeader);
