/**
 * Contains helper methods for traversing / manipulating / ... HTML.
 */
export default class HtmlUtils {
    /**
     * Returns ancestor-element of element if ancestor has css-class
     * @param {Element} element
     * @param {String} cssClass
     * @return {element|null}
     */
    static ancestorHasClass(element, cssClass) {
        while (element && element !== document) {
            if (element.classList.contains(cssClass)) {
                return element;
            }
            element = element.parentNode;
        }
        return null;
    }

    /**
     * Returns ancestor-element of element if ancestor has html-attribute
     * @param {Element} element
     * @param {String} attribute
     * @return {element|null}
     */
    static ancestorHasAttr(element, attribute) {
        while (element && element !== document) {
            if (element.hasAttribute(attribute)) {
                return element;
            }
            element = element.parentNode;
        }
        return null;
    }

    /**
     * Returns cssClass modified from cssSelector
     * @param {String} cssSelector
     * @return {String} cssClass
     */
    static cssSelectorToClass(cssSelector) {
        var cssClass = cssSelector.slice(1);
        return cssClass;
    }
}
